/* ==========================================================================
 * OFFSCREEN NAVIGATION
 * ========================================================================== */

// these variables should also match the JS variables in _navOffscreen.js
@offscreen-nav-width: 290px;

/* ==========================================================================
 * OFFSCREEN NAVIGATION
 * ========================================================================== */

body {
  &.no-scroll {
    position: fixed;
    overflow: hidden;
    width: 100%;
    min-height: 100%;
  }
}

/* ==========================================================================
 * - FUNCTIONAL
 * ========================================================================== */

.offscreen-nav {
  display: block;
}

.no-js .offscreen-nav {
  position: absolute;
  display: block;
  top: 0;
  -webkit-overflow-scrolling: touch;
  left: -@offscreen-nav-width;

  &:target {
    left: 0;
  }

  &.is-right {
    left: auto;
    right: -@offscreen-nav-width;

    &:target {
      left: auto;
      right: 0;
    }
  }

  .nav-inner {
    padding: 0;
    width: auto;
    max-width: none;
    position: relative;
  }
}

.js {
  .offscreen-nav {
    position: absolute;
    display: none;
    top: 0;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;

    &.is-active {
      display: block;
    }

    .nav-inner {
      padding: 0;
      width: auto;
      max-width: none;
      position: relative;
    }
  }
}

// touch states to ensure you can't interact with items that aren't visible
.js-nav-open {
  // offscreen navigation
  .offscreen-nav.is-active {
    // note that the defaults are different here
    // this is to resolve text selection bugs in iOS Safari
    -webkit-touch-callout: default;
    -webkit-user-select: auto;
    -khtml-user-select: auto;
    -moz-user-select: text;
    -ms-user-select: text;
    user-select: text;
  }

  // page content
  #inner-wrap {
    // ensures the user can't select any text inside the page when the navigation is open
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
}

  .js {
    //offscreen navigation
    .offscreen-nav {
      position: fixed;
      height: 100%;
      width: @offscreen-nav-width;
      z-index: @zindex-offscreen-nav;
      overflow: hidden;
      left: -@offscreen-nav-width;

      &.is-right {
        left: auto;
        right: -@offscreen-nav-width;
      }

      &.has-css-scroll {
        overflow-y: scroll;
      }
    }

      &.js-nav-translate .offscreen-nav {
        left: auto;
        transform: translate3d(-@offscreen-nav-width, 0, 0);

        &.is-right {
          left: auto;
          right: 0;
          transform: translate3d(@offscreen-nav-width, 0, 0);
        }
      }
  }

  .js-nav-open {
    // navigation drawer
    .offscreen-nav.is-active {
      left: 0;

      &.is-right {
        left: auto;
        right: 0;
      }
    }

      &.js-nav-translate .offscreen-nav.is-active {
        left: auto;
        transform: translate3d(0, 0, 0);

        &.is-right {
          right: 0;
          transform: translate3d(0, 0, 0);
        }
      }
  }

/* ==========================================================================
 * - DESIGN
 * ========================================================================== */

@offscreen-nav-padding-left: 40px;
@offscreen-nav-padding-right: 60px;

.offscreen-nav {
  outline: none;
  background-color: @tcom-offscreen-nav-bg;
  color: @tcom-offscreen-nav-link;

  a {
    color: @tcom-offscreen-nav-link;
    display: block;
    padding: 0 @offscreen-nav-padding-right 0 @offscreen-nav-padding-left;
    text-decoration: none;
    transition: color 250ms;

    .td-icon {
      color: @tcom-offscreen-nav-icon;
      margin-right: 10px;
      top: -3px;
      transition: color 250ms;
      .font-size(22);
    }

    &:hover,
    &:focus,
    &:active {
      color: @tcom-light-on-dark-color;

      .td-icon {
        color: @tcom-light-on-dark-color !important;  //because of the default icon style that overrides everything
      }
    }

    &:hover,
    &:active {
      outline: none;
    }
  }

  .site-logo {
    padding: 0;
    position: absolute;
    top: 20px;
    left: 20px;
  }

  .nav-close {
    padding: 0;
    color: @tcom-offscreen-nav-link;
    width: 60px;
    height: 67px;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    text-align: center;
    z-index: 5;
    background-color: transparent;
    border: none;

    &:before {
      color: @tcom-offscreen-nav-link;
      .td-icon-family(icon-ui-cross);
      .font-size(20, 67);
    }

    &:hover,
    &:focus,
    &:active {
      color: @tcom-light-on-dark-color;

      &:before {
        color: @tcom-light-on-dark-color;
      }
    }
  }
}

.offscreen-nav-primary,
.offscreen-nav-global {
  list-style: none;
  padding: 0;
  margin: 0;
}

  .offscreen-nav-primary {
    .children {
      display: none;
      outline: none;

      ul {
        list-style: none;
        padding: 0;
        margin: 0;
      }
    }

    .is-expanded > .children {
      display: block;
    }

    .children.is-displayed {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: @tcom-offscreen-nav-bg;
      z-index: 2;
    }
  }

/* ==========================================================================
 * -- PRIMARY NAV DESIGN
 * ========================================================================== */

.offscreen-nav-primary {
  padding-top: 67px;
  .font-size(14);
  .font-gravur();

  li {
    margin: 0;
  }

  & > li:first-child {
    border-top: 1px solid @tcom-offscreen-nav-border;
  }

  a {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .nav-lvl-1 {
    padding-top: 22px;
    padding-bottom: 22px;
    border-bottom: 1px solid @tcom-offscreen-nav-border;
    .font-size(16);
  }

  .has-children {
    position: relative;

    &:after {
      .td-icon-family(icon-ui-right-arrow);
      .font-size(16, 16);
      position: absolute;
      top: 50%;
      right: 40px;
      margin-top: -8px;
    }
  }

    .is-expanded {
      & > .children {
        border-bottom: 1px solid @tcom-offscreen-nav-border;
        padding-top: 0;
        padding-bottom: 15px;
      }

      & > .has-children {
        border-bottom: none;
        padding-bottom: 15px;
        .font-size(24);

        &:after {
          display: none;
        }
      }
    }

  .back-to-parent-container {
    border-bottom: 1px solid @tcom-offscreen-nav-border;
    padding-right: @offscreen-nav-padding-right;
    position: relative;

    &:after {
      content: '';
      width: 1px;
      height: 60%;
      border-right: 1px solid @tcom-offscreen-nav-border;
      position: absolute;
      top: 20%;
      right: 60px;
    }
  }

  .back-to-parent {
    position: relative;
    padding-top: 22px;
    padding-bottom: 22px;
    padding-right: 20px;
    font-size: 16px;

    &:before {
      .td-icon-family(icon-ui-left-arrow);
      .font-size(18, 18);
      position: absolute;
      top: 50%;
      left: 15px;
      margin-top: -9px;
    }
  }

  .parent-link {
    font-size: 24px;
    margin-top: 15px;
    margin-bottom: 5px;

    .td-icon {
      font-size: 30px;
      position: absolute;
      top: 12px;
      left: @offscreen-nav-padding-left;
    }

    &.has-icon {
      position: relative;
      padding-left: @offscreen-nav-padding-left + 40px;
      padding-right: @offscreen-nav-padding-right - 20px;
    }
  }

  & .is-active > a,
  & .is-active > .children > .parent-link,
  & .is-expanded.is-active > a {
    color: @tcom-light-on-dark-color;
    position: relative;

    .td-icon {
      color: @tcom-light-on-dark-color !important; //because of the default icon style that overrides everything
    }

    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: @offscreen-nav-padding-left - 15px;
      margin-top: -4px;
      transition: border-left-color 250ms;
      .shape-triangle-right(5px, 9px);
      .with-tcom-theme({
        border-left-color: @primary;
      });
    }
  }

  & .is-active > .has-children {
    color: @tcom-offscreen-nav-link;

    .td-icon {
      color: @tcom-offscreen-nav-icon !important; //because of the default icon style that overrides everything
    }

    &:before {
      .with-tcom-theme({
        border-left-color: @tcom-offscreen-nav-icon; //overriding the theme colours
      });
    }

    &:hover,
    &:focus,
    &:active {
      color: @tcom-light-on-dark-color;

      .td-icon {
        color: @tcom-light-on-dark-color !important;  //because of the default icon style that overrides everything
      }

      &:before {
        .with-tcom-theme({
          border-left-color: @tcom-light-on-dark-color; //overriding the theme colours
        });
      }
    }
  }

    & .is-expanded.is-active > a:before {
      margin-top: -2px;

      .with-tcom-theme({
        border-left-color: @primary;
      });
    }

}

/* ==========================================================================
 * -- GLOBAL NAV DESIGN
 * ========================================================================== */

.offscreen-nav-global {
  font-size: 14px;
  padding: 30px 0;

  li {
    margin: 18px 0 0 0;

    &:first-child {
      margin-top: 0;
    }
  }
}
