.show-hide {

  .more-text { display: none; }

  .show-hide-toggle,.show-hide-link {
    text-align: right;
    margin-bottom: @tcom-col-content-margin;
    line-height: 14px;
  }

  .show-more { display: inline-block; }
  .show-less { display: none; }
  
  .opened {
    .show-more { display: none; }
    .show-less { display: inline-block; }
  }
}
