/* ==========================================================================
 * PRELOADER OVERLAY
 * ========================================================================== */

.preloader-container {
  position: relative;
  min-height: 256px;
  outline: none;
}

.preloader-overlay {
  background-color: rgba(255, 255, 255, 0.4);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 50;

  .indicator {
    width: 48px;
    height: 48px;
    position: absolute;
    top: 40px;
    left: 50%;
    //margin-top: -24px;
    margin-left: -24px;
    z-index: 1;
    background: transparent url(../img/telstra/forms/loader.png) no-repeat 0 0;

    .with-tcom-theme({
      background-image: url(~"../img/telstra/forms/loader-@{themeName}.png");
    });
  }
}
