@media @big-screen {
// commun styles between searchMobile and searchDesktop
  @tcom-img-search-size: 50px;

  .search-telstra {
    position: relative;

    .form-control { width: 100%; }

    .twitter-typeahead { display: block !important; }

    .menu-item-sm { border: 0 !important; }

    .menu-item-search-header { border-bottom: 1px solid @tcom-grey-darker !important; }

    .menu-item-lg {
      .clearfix();
      h6,
      p {
        padding-left: @tcom-img-search-size + @tcom-global-content-padding/2;
        margin: 0;
      }
    }

    .menu-icon {
      width: @tcom-img-search-size;
      height: @tcom-img-search-size;
      text-align: center;
    }
  }

  // ----------------------------------------------
  // styles only for desktop

  #searchDesktop {
    width: 210px;

    .search-telstra-btn {
      position: absolute;
      top: 0;
      right: 0;
    }

    .tt-dropdown-menu {
      border: 1px solid darken(@tcom-light-grey, 10%);
      border-top: none;
      background-color: white;
      width: 280px;
      top: 38px !important;
      right: 0px !important;
      left: auto !important;
    }

    .menu-item-sm,
    .menu-item-lg {
      padding: @tcom-global-content-spacing (2 * @tcom-global-content-spacing);
      color: @tcom-grey;
      text-decoration: none;
      display: block;

      &:focus,
      &:hover,
      &:active { 
        background-color: @tcom-light-grey;
      }
    }

    .menu-item-lg { border-bottom: 1px solid darken(@tcom-light-grey, 10%) !important; }

    .tt-cursor {
      .menu-item-sm,
      .menu-item-lg {
        background-color: @tcom-light-grey;
      }
    }

    .menu-item-search-header {
      border-bottom: 1px solid darken(@tcom-light-grey, 10%) !important;
      background-color: transparent !important;
    }

    .menu-icon { float: left; }
  }
}
