.help-tooltip {
  border-bottom: 1px dotted;
}

.help-tooltip-icon {
  background: url(../img/telstra/ic-help.png) no-repeat 1px 1px;
  display: inline-block;
  height: 19px;
  width: 19px;
  cursor: pointer;
  vertical-align: middle;
  border-bottom: 0;
}

.popover {
  font-size: 12px;
  color: @tcom-grey-light;

  > .arrow {
    border-width: 8px;
  }

  &.right > .arrow {
    margin-top: -8px;
    left: -8px;
  }

  &.left > .arrow {
    margin-top: -8px;
    right: -8px;
  }

  &.top > .arrow {
    bottom: -8px;
    margin-left: -9px;
  }

  &.bottom > .arrow {
    top: -8px;
    margin-left: -9px;
  }
}

.popover-content {
  padding: 5px 10px;
}

.popover-title {
  display: none;
}

.popover-overlay {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0px;
  z-index: @zindex-popover - 1;
}