@font-face {
  font-family: 'td-icons';
  src:url('../fonts/td-original-icons.eot');
  src:url('../fonts/td-original-icons.eot#iefix') format('embedded-opentype'),
    url('../fonts/td-original-icons.woff') format('woff'),
    url('../fonts/td-original-icons.ttf') format('truetype'),
    url('../fonts/td-original-icons.svg#td-original-icons') format('svg');
  font-weight: normal;
  font-style: normal;
}

/* hover state for icons, require icon to be inside an <a> tag and have the icon-hover class applied */
.td-icon-hover {
  &:hover {
    text-decoration: none;
    color: @tcom-white !important;
    .generate-theme(s; background-color, light);

    .icon-lock {
      color: @tcom-white !important;
      border: 1px solid @tcom-white;
      .generate-theme(s; background-color, lighter);
    }
  }
}

.td-icon,
.td-icon-xs,
.td-icon-sm,
.td-icon-md,
.td-icon-lg,
.td-icon-xl {
  position: relative;
  font-family: 'td-icons';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  vertical-align: middle;
  color: @tcom-grey-light !important;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;


   /* if icon-lock is a nested icon in another icon, it gets its own styling */
  .icon-lock{
    position: absolute;
    top: -10px;
    right: -10px;
    padding: 4px !important;
    border-radius: 20px;
    border: 1px solid transparent;
  }

  p {
    margin: 0;
    font-size: @font-size-base;
    font-family: @font-family-base;
    line-height: 18px;
    color: @tcom-black;

    a& {
      .generate-theme(s; color, dark);
    }
  }
}

.td-icon-xs { font-size: @tcom-icon-xs; }
.td-icon-sm { font-size: @tcom-icon-sm; }
.td-icon-md { font-size: @tcom-icon-md; }
.td-icon-lg { font-size: @tcom-icon-lg; }
.td-icon-xl { font-size: @tcom-icon-xl; }

