//
// Lightbox Styles
// --------------------------------------------------
.modal {
  height: auto !important; /* TEL-1809 : when "wcmode=disabled", .modal's height is set around 250px even if the content is heighter */
  overflow: hidden;
}

.modal-header,
.modal-header-no-border {
  padding: @modal-title-padding 0;
  margin: 0 @modal-title-padding;
}

.modal-header-no-border {
  border: none;
}

.modal-header {
  .modal-title {
    padding-bottom: 0;
  }
}

.modal-header, 
.modal-header-no-border {
  .close { margin-top: 0; } 
}

.modal-body {
  overflow-y: auto;
}

.print-lightbox & {
  float:right;
  margin-right:30px;  
  margin-top: 2px;
}

.iframe & {
  position: relative;
  min-height: 480px;
  min-width: 100%;
}

.modal-small & {
  width: 530px;
  @media @small-screen {
    width: auto;
  }
}
.modal-medium & {
  width: 638px;
  @media @small-screen {
    width: auto;
  }
}
.modal-large & {
  width: 800px;
  max-width: 100%;
  @media @small-screen {
    width: auto;
  }
}
.modal-xlarge & {
  width: 982px;
  max-width: 100%;
  @media @small-screen {
    width: auto;
  }
}

/* cursor */
a[data-toggle="modal"] & {
 cursor: pointer;
}

//video player component inside lightbox width
.modal {
  .video-player {
    margin: -@modal-inner-padding;
  }
  .video h3{
    display: none;
  }
}