@collapse-bar-bg-closed: @tcom-grey-lighter;
@collapse-bar-bg-open: @tcom-globalnav-highlight;



.accordion.section {
  margin-bottom: @tcom-row-group-margin;
}


.collapse-bar {
  position: relative;
  display: block;
  margin: 15px -15px 15px 36px;
  padding: 0 20px;

  @media @small-screen {
    margin: 15px -15px 15px -15px;
    padding: 12px 40px 12px 20px;
    line-height: 25px;
    height: auto;
  }

  background: @collapse-bar-bg-open;
  color: @tcom-white !important;
  height: 50px;
  font-size: 20px;
  line-height: 50px;
  -webkit-touch-callout: none;
  user-select: none;

  &:hover,
  &:focus {
    color: @tcom-text-light;
    text-decoration: none;
    cursor: pointer;
  }

  &:after {
    display: block;
    position: absolute;
    top: 0;
    left: -51px;
    width: 50px;
    height: 50px;
    line-height: 50px;

    font-size: 30px;
    text-align: center;
    .td-icon-family(icon-ui-minus);
    background: @collapse-bar-bg-open;

    @media @small-screen {
      width: 0;
      right: 50px;
      left: auto;
      .td-icon-family(icon-ui-up-arrow-thick);
    }
  }

  &.collapsed {
    color: @tcom-text-dark !important;
    background: @collapse-bar-bg-closed;

    &:after {
      background: @collapse-bar-bg-closed;
      .td-icon-family(icon-ui-plus);

      @media @small-screen {
      .td-icon-family(icon-ui-down-arrow-thick);
      }
    }
  }
}

.collapse-content {
  margin: 0 -15px;
  margin-top: -14px;
  padding: 0 15px;
  .clearfix();
  background: #fff;
  margin-bottom: 1px;

  &.transparent {
    background: transparent;
  }

  &.collapse-content-last {
    border-bottom: 1px solid @collapse-bar-bg-open;
    margin-bottom: 15px;
  }

  + .collapse-bar {
    margin-top: 20px;
  }

  .standard.row.content {
    margin-bottom: 0;
  }
}