/* ==========================================================================
 * NEW THEME COLOURS
 * ========================================================================== */

@tcom-theme-blue-primary: #0099d2;
@tcom-theme-blue-secondary: #001e82;
@tcom-theme-blue-gradient-start: #00baff;
@tcom-theme-blue-gradient-end: #5a1991;

@tcom-theme-magenta-primary: #de008d;
@tcom-theme-magenta-secondary: #a0148c;
@tcom-theme-magenta-gradient-start: #de008d;
@tcom-theme-magenta-gradient-end: #640064;

@tcom-theme-green-primary: #84bb28;
@tcom-theme-green-secondary: #005055;
@tcom-theme-green-gradient-start: #bcda07;
@tcom-theme-green-gradient-end: #007478;

@tcom-theme-orange-primary: #f05a00;
@tcom-theme-orange-secondary: #ab004c;
@tcom-theme-orange-gradient-start: #ffa500;
@tcom-theme-orange-gradient-end: #e80070;

@tcom-theme-grey-primary: #757575;
@tcom-theme-grey-secondary: #4f4f4f;
@tcom-theme-grey-gradient-start: #757575;
@tcom-theme-grey-gradient-end: darken(@tcom-theme-grey-secondary, 10%);

@tcom-theme-default-primary: @tcom-theme-blue-primary;
@tcom-theme-default-secondary: @tcom-theme-blue-secondary;
@tcom-theme-default-gradient-start: @tcom-theme-blue-gradient-start;
@tcom-theme-default-gradient-end: @tcom-theme-blue-gradient-end;

/* ==========================================================================
 * WITH TCOM THEME MIXIN
 * ========================================================================== */

// NOTE: @themes variable defined in mixins.less as part of the original mixin

.with-tcom-theme(@rules) {
  // Loop through theme list
  .-(@i: length(@themes)) when (@i > 0) {

    & {
      @themeName: e(extract(@themes, @i));

      // Call .theme-helper(...) mixin for a single css property
      .base-@{themeName} & {
        @p: ~'tcom-theme-@{themeName}-primary';
        @s: ~'tcom-theme-@{themeName}-secondary';
        @gs: ~'tcom-theme-@{themeName}-gradient-start';
        @ge: ~'tcom-theme-@{themeName}-gradient-end';

        @primary: @@p;
        @secondary: @@s;
        @gradient-start: @@gs;
        @gradient-end: @@ge;

        @rules();
      }

      .-(@i: @i - 1);
    }

  } .-;
}

/* ==========================================================================
 * - NEW WITH-TCOM-THEME CLASSES
 * ========================================================================== */

/* ==========================================================================
 * -- BACKGROUNDS
 * ========================================================================== */

.tcom-theme-bg-primary {
  .with-tcom-theme({
    background-color: @primary !important;
  });
}

.tcom-theme-bg-secondary {
  .with-tcom-theme({
    background-color: @secondary !important;
  });
}

.tcom-theme-bg-0 {
  .with-tcom-theme({
    background-color: @gradient-start !important;
  });
}

.tcom-theme-bg-33 {
  .with-tcom-theme({
    background-color: mix(@gradient-end, @gradient-start, 33%);
  });
}

.tcom-theme-bg-50 {
  .with-tcom-theme({
    background-color: mix(@gradient-end, @gradient-start, 50%);
  });
}

.tcom-theme-bg-66 {
  .with-tcom-theme({
    background-color: mix(@gradient-end, @gradient-start, 66%);
  });
}

.tcom-theme-bg-100 {
  .with-tcom-theme({
    background-color: @gradient-end !important;
  });
}

/* ==========================================================================
 * -- GRADIENTS
 * ========================================================================== */

.tcom-theme-bg-primary-secondary {
  .with-tcom-theme({
    background-color: @secondary !important;
    background: linear-gradient(to right, @primary 0%, @secondary 100%);
  });
}

.tcom-theme-bg-start-end {
  .with-tcom-theme({
    background-color: @secondary !important;
    background: linear-gradient(to right, @gradient-start 0%, @gradient-end 100%);
  });
}

.tcom-theme-bg-vertical-start-end-70 {
  .with-tcom-theme({
    background-image: none !important;
    background-color: @gradient-start;
    background: linear-gradient(to bottom, @gradient-start 0%, @gradient-end 70%) !important;
  });
}
/* ==========================================================================
 * -- FOREGROUND
 * ========================================================================== */

.tcom-theme-fg-primary {
  .with-tcom-theme({
    color: @primary;
  });
}

.tcom-theme-fg-secondary {
  .with-tcom-theme({
    color: @secondary;
  });
}

.tcom-theme-fg-0 {
  .with-tcom-theme({
    color: @gradient-start;
  });
}

.tcom-theme-fg-33 {
  .with-tcom-theme({
    color: mix(@gradient-end, @gradient-start, 33%);
  });
}

.tcom-theme-fg-50 {
  .with-tcom-theme({
    color: mix(@gradient-end, @gradient-start, 50%);
  });
}

.tcom-theme-fg-66 {
  .with-tcom-theme({
    color: mix(@gradient-end, @gradient-start, 66%);
  });
}

.tcom-theme-fg-100 {
  .with-tcom-theme({
    color: @gradient-end;
  });
}
