//
// Telstra mixins
// --------------------------------------------------

.noselect() {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}


/* ==========================================================================
 * FONTS
 * ========================================================================== */

.font-size(@font-size, @line-height: false) {
  font-size: unit(@font-size, px);
  font-size: unit(@font-size / @FONT_BASE, rem);

  & when not (@line-height = false) {
    line-height: @line-height / @font-size;
  }
}

.font-gravur(@font-size: inherit, @font-weight: normal) {
  font-family: @font-family-gravur;
  text-transform: uppercase;

  & when (isnumber(@font-size)) {
    font-size: unit(@font-size, px);
  };

  & when (isstring(@font-weight)) {
    font-weight: @font-weight;
  };
}

/* ==========================================================================
 * CSS TRIANGLES
 * ========================================================================== */

.shape-triangle-up(@width, @height, @color: #fff) {
  width: 0;
  height: 0;
  border: @width/2 solid transparent;
  border-bottom: @height solid @color;
}

.shape-triangle-down(@width, @height, @color: #fff) {
  width: 0;
  height: 0;
  border: @width/2 solid transparent;
  border-top: @height solid @color;
}

.shape-triangle-left(@width, @height, @color: #fff) {
  width: 0;
  height: 0;
  border: @height/2 solid transparent;
  border-right: @width solid @color;
}

.shape-triangle-right(@width, @height, @color: #fff) {
  width: 0;
  height: 0;
  border: @height/2 solid transparent;
  border-left: @width solid @color;
}

.triangle(@direction, @width, @height, @color: #fff) {
  content: '';

  & when (@direction = up) {
    .shape-triangle-up(@width, @height, @color);
  }

  & when (@direction = down) {
    .shape-triangle-down(@width, @height, @color);
  }

  & when (@direction = left) {
    .shape-triangle-left(@width, @height, @color);
  }

  & when (@direction = right) {
    .shape-triangle-right(@width, @height, @color);
  }
}

// Theme button generator
.base-theme-buttons(@default; @light; @dark) {
  color: @tcom-light-on-dark-color;

  &.do-business {
    &,
    &:after {
      background-color: @default;
    }

    &:hover,
    &:hover:after,
    &:focus,
    &:focus:after {
      background-color: @dark;
    }

    &:active,
    &:active:after {
      background-color: darken(@dark, 10%);
    }
  }

  &.primary {
    &,
    &:after {
      background-color: @dark;
    }

    &:hover,
    &:hover:after,
    &:focus,
    &:focus:after {
      background-color: @default;
    }

    &:active,
    &:active:after {
      background-color: darken(@light, 10%);
    }
  }

  &.light {
    color: @dark;

    &,
    &:after {
      background-color: @tcom-default-light-bg;
    }

    &:hover,
    &:hover:after,
    &:focus,
    &:focus:after {
      // IE8 fix
      .lt-ie9 & {
        background: @tcom-default-light-bg;
      }

      background-color: fade(@tcom-default-light-bg, 75%);
    }

    &:active,
    &:active:after {
      color: @tcom-default-light-bg !important;
      background-color: darken(@dark, 10%) !important;
    }
  }

  &.dropdown-toggle,
  &.chevron-down {
    &:after,
    &:hover:after,
    &:focus:after,
    &:active:after {
      background: transparent;
    }
  }
}

/* Gradient generators
----------------------------------*/
.gradient (@startColor: #eee, @endColor: white) {
  .vertical-gradient(@startColor, @endColor);
}

.horizontal-gradient (@startColor: #eee, @endColor: white) {
  background: @startColor; /* Old browsers */
  background: -moz-linear-gradient(left,  @startColor 0%, @endColor 100%); /* FF3.6+ */
  background: -webkit-gradient(linear, left top, right top, color-stop(0%,@startColor), color-stop(100%,@endColor)); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(left,  @startColor 0%,@endColor 100%); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(left,  @startColor 0%,@endColor 100%); /* Opera 11.10+ */
  background: -ms-linear-gradient(left,  @startColor 0%,@endColor 100%); /* IE10+ */
  background: linear-gradient(to right,  @startColor 0%,@endColor 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='@{startColor}', endColorstr='@{endColor}',GradientType=1 ); /* IE6-8 */
}

.vertical-gradient (@startColor: #eee, @endColor: white, @startPerc: 0%, @endPerc: 100%) {
  background: @startColor; /* Old browsers */
  background: -moz-linear-gradient(top,  @startColor @startPerc, @endColor @endPerc); /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(@startPerc,@startColor), color-stop(@endPerc,@endColor)); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top,  @startColor @startPerc,@endColor @endPerc); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(top,  @startColor @startPerc,@endColor @endPerc); /* Opera 11.10+ */
  background: -ms-linear-gradient(top,  @startColor @startPerc,@endColor @endPerc); /* IE10+ */
  background: linear-gradient(to bottom,  @startColor @startPerc,@endColor @endPerc); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='@{startColor}', endColorstr='@{endColor}',GradientType=0 ); /* IE6-9 */
}


