/* Themes Mixin
---------------------------------------------*/

// Available themes
@themes: 'default', 'green', 'orange', 'blue', 'magenta', 'grey';

// Available color per theme
/*
  [1]      [2]      [3]     [4]      [5]
lighter| light  | default | dark  | darker;
---------------------------------------------
*/
@grey-colors:       #8f8f8f, #757575, #575757, #4f4f4f, #363636;
@orange-colors:     #ff9f33, #ff8700, #f05a00, #ab004c, #780035;
@green-colors:      #bcfb00, #96c800, #009b46, #005a28, #002711;
@turquoise-colors:  #00eccc, #00b9a0, #009182, #004b46, #001816;
@blue-colors:       @tcom-blue-lighter, @tcom-blue-light, @tcom-blue, @tcom-blue-dark, @tcom-blue-darker;
@purple-colors:     #bab3e8, #968cdc, #644bc8, #46008c, #2c0059;
@magenta-colors:    #ff5baf, #ff2896, #a0148c, #640064, #310031;

@default-colors: @blue-colors;


/* Theme helper
-------------------------------------------------*/

.theme-helper(@themeName; @property; @value1; @value2; @value3){

    // Theme color mapping base on the position in each color list
    @colorScheme: ~'@{themeName}-colors';
    @lighter: 1;
    @light: 2;
    @default: 3;
    @dark:  4;
    @darker:  5;

    // Generate color theme for foreground, background and borders
    & when (@property = color), (@property = border-color), (@property = background-color), (@property = border-left-color), (@property = border-right-color), (@property = border-top-color), (@property = border-bottom-color){

        // Define color and function color variable mapping names
        @color: ~'@{value1}';
        @functionColor: ~'@{value2}';

        & when (@value1 = default), (@value1 = light), (@value1 = dark), (@value1 = lighter), (@value1 = darker){
          @{property}: extract(@@colorScheme, @@color);
        }

        & when (@value1 = white), (@value1 = black){
          @{property}: @value1;
        }

        //Call to LESS built-in mixin lighten(...)
        & when (@value1 = lighten){

          & when (@value2 = default), (@value2 = light), (@value2 = dark), (@value2 = lighter), (@value2 = darker){

            @fColor : extract(@@colorScheme, @@functionColor);
            @{property}: lighten(@fColor, @value3);

          }

          & when (@value2 = white), (@value2 = black){
            @{property}: lighten(@value2, @value3);
          }
        }

        // Call to LESS built-in mixin darken(...)
        & when (@value1 = darken){

          & when (@value2 = default), (@value2 = light), (@value2 = dark), (@value2 = lighter), (@value2 = darker){

            @fColor : extract(@@colorScheme, @@functionColor);
            @{property}: darken(@fColor, @value3);

          }

          & when (@value2 = white), (@value2 = black){
            @{property}: darken(@value2, @value3);
          }
        }

      }

   //Generate color theme for background gradients
   & when (@property = background){

        // Define colorFrom and colorTo variable mapping names
        @colorFrom: ~'@{value2}';
        @colorTo: ~'@{value3}';

        // Generate background linear-gradient rule
        & when (@value1 = linear-gradient){

          & when (@value2 = white), (@value2 = black){

            & when (@value3 = white), (@value3 = black){
              @{property}: linear-gradient(@value2, @value3);
            }


            & when (@value3 = default), (@value3 = light), (@value3 = dark), (@value3 = lighter), (@value3 = darker){

              @valColorTo: extract(@@colorScheme,  @@colorTo);
              @{property}: linear-gradient(@value2, @valColorTo);
            }

          }

          & when (@value2 = default), (@value2 = light), (@value2 = dark), (@value2 = lighter), (@value2 = darker){

            & when (@value3 = white), (@value3 = black){
             @valColorFrom: extract(@@colorScheme,  @@colorFrom);
             @{property}: linear-gradient(@valColorFrom, @value3);
           }

           & when (@value3 = default), (@value3 = light), (@value3 = dark), (@value3 = lighter), (@value3 = darker){

            @valColorFrom: extract(@@colorScheme,  @@colorFrom);
            @valColorTo: extract(@@colorScheme,  @@colorTo);

            @{property}: linear-gradient(@valColorFrom, @valColorTo);
          }
        }

      }
    }

  }

/* Theme Color Main
-------------------------------------------------*/

.generate-theme(@mode; @rest...) {

     // Loop through theme list
     .-(@i: length(@themes)) when (@i > 0) {
      @themeName: e(extract(@themes, @i));

         // Call .theme-helper(...) mixin for a single css property
         .base-@{themeName} & {
          .-(@length: length(@rest)) when (@mode = s), (@mode = single) {
            @property: e(extract(@rest, 1));
            @value1: extract(@rest, 2);
            @value2: extract(@rest, 3);
            @value3: extract(@rest, 4);

            .theme-helper(~'@{themeName}'; @property; @value1; @value2; @value3);
            } .-;

            // Call .theme-helper(...) mixin for multiple css properties
            .-(@length: length(@rest)) when (@mode = m), (@mode = multi) {
              .-(@j: length(@rest)) when (@j > 0) {
                @declaration: e(extract(@rest, @j));

                @property : e(extract(@declaration, 1));
                @value1: extract(@declaration, 2);
                @value2: extract(@declaration, 3);
                @value3: extract(@declaration, 4);

                .theme-helper(~'@{themeName}'; @property; @value1; @value2; @value3);

                .-((@j - 1));
                } .-;
                } .-;


              }

              .-((@i - 1));
              } .-;
            }

