/* ==========================================================================
 * FUNCTIONAL STYLES
 * ========================================================================== */

.visuallyhidden() {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.undovisuallyhidden() {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

.list-reset() {
  margin: 0;
  padding: 0;
  list-style: none;
}

.ellipsis() {
  text-overflow: ellipsis;
  vertical-align: top;
  white-space: nowrap;
  overflow: hidden;
}