.dropdown-menu {
  background: transparent;
  .generate-theme(s; color, white;);
  border: 0;
  margin: 0;
  padding: 0;


  // hover dropdowns is enabled only for desktop
  @media @big-screen {
    .contact-share:hover & {
      display: block; 
    }
  }


  > li > a {
    .generate-theme(m; color, white; background-color, dark);
    padding: 12px 24px;
    margin: 1px 0 0 0;
    position: relative;

    &:after {
      .td-icon-family(icon-ui-right-arrow);

      position: absolute;
      display: block;
      right: 0;
      top: 0;
      width: 40px;
      height: 44px;
      text-align: center;

      font-size: 24px;
      font-style: normal;
      font-weight: normal;
      line-height: 44px;

      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
    }

    &:hover,
    &:focus {
      .generate-theme(s; background-color, light);
    }

    &:active {
      .generate-theme(s; background-color, light);
    }
  }
}

.btn-group.open .btn.dropdown-toggle {
  -webkit-box-shadow: none;
  box-shadow: none;

  &.secondary {
    .generate-theme(m; color, white; background-color, dark);
    
    &:after {
      background: transparent !important;
      .generate-theme(s; color, white);
    }
  }
  
  &.do-business {
    .generate-theme(s; background-color, dark);
  }

  &.primary {
    .generate-theme(s; background-color, default);
  }
}