//
// Table styles
// --------------------------------------------------

.table {
  & > thead,
  & > tbody,
  & > tfoot {
    & > tr {
      & > th,
      & > td {
        padding: 10px;
      }
    }
  }

  th, td {
    border: 1px solid @table-border-color !important;
  }
  th {
    background-color: @table-title-bg !important;
    text-align: left;
  }
}

.table-fixed {
  table-layout: fixed;
}

.no-title-bg {
  th {
    background-color: @tcom-white !important;
    text-align: left;
    border-bottom-width: 1px !important;
  }
}
.table-title-dark {
  th {
    background-color: @table-plan-offer-bg !important;
    color: @tcom-white;
  }    
}

.table-title-normal {
  th {
    font-weight: normal;
  }
}

.table-cell-spacing {
  border: 0px solid !important;
  th {
    border-color: @tcom-white !important;
    border-width: 1px 15px 1px 0 !important;
    border-bottom: 1px solid @table-cell-spacing-border !important;
  }
  td {
    border-color: @tcom-white @tcom-white @table-title-bg !important;
    border-width: 1px 15px 1px 0 !important;
  }          
}


.table-wrap {
  width: 100%;
  overflow: hidden;
  position: relative;
  margin-bottom: @line-height-computed;
  padding: 6px 0;
  border-left: 1px solid rgba(0,0,0,0);
  border-right: 1px solid rgba(0,0,0,0);

  .table-responsive {
    border: 0;
  }

  .table-responsive,
  .table {
    margin-bottom: 0;
  }

  @media @small-screen {

    &:after,
    &:before {
      position: absolute;
      top: 0;
      width: 50px;
      height: 100%;
      content: '';
      opacity: 0;
      .transition(opacity ease-in-out .3s);
    }

    &:after {
      left: 100%;
      border-radius: ~"10px 0 0 10px / 50% 0 0 50%";
      box-shadow: -4px 0 10px black;
    }

    &:before {
      right: 100%;
      border-radius: ~"0 10px 10px 0 / 0 50% 50% 0";
      box-shadow: 4px 0 10px black;
    }

    &.rigth,
    &.left {
      .transition(border ease-in-out .3s);
    }

    &.right {
      border-right: 1px solid @table-border-color;

      &:after { 
        opacity: .25;
      }
    }

    &.left {
      border-left: 1px solid @table-border-color;

      &:before { 
        opacity: .25;
      }
    } 
  }

}

/*** TEL-2617: changes to center align the detials in life long section of prepaid handsets ***/
.align-middle {
  text-align: center !important;
  vertical-align: middle !important;

  & > .content-sub-heading {
    text-align: left;
    padding-bottom: 0px;
  }
}