/* ---------------------------- */
.input-tcom-lg {
  // Input text field height large
  height: @tcom-button-height;
}


/* ---------------------------- */
fieldset {

  // border on fieldset
  &.border {
    padding: @tcom-global-content-padding;
    border: 1px solid @tcom-grey-lighter;

    .form-group.last {
      margin-bottom: 0;
    }
  }
}


/* ---------------------------- */
.radio-inline {

  @media @small-screen {
    // radio-inline stacks on mobile and are easier to click
    &,
    & + .radio-inline {
      display: block;
      margin-left: 0 !important;
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }

  .td-label + & {
    // increase space between td-label and radio-inline
    margin-left: @tcom-global-content-spacing * 2;
  }
}


/* ---------------------------- */
label,
.td-label { 
  // td-label is used to force an element to look like a label
  display: inline-block;
  margin-bottom: 5px;
  font-weight: bold;

  & > .label-complement {
    display: block;
    font-weight: lighter;
    color: @tcom-grey;
  }
}


/* ---------------------------- */
textarea { 
  // textarea's width cannot be resized by user
  min-width: 100%;
  max-width: 100%;
  width: 100%;
}


/* ---------------------------- */
.alert {
  form & {
    // in a form, alert elements are hidden by default 
    display: none;
  }
}


/* ---------------------------- */
.mandatory {
  color: @tcom-mandatory-red;
  font-size: 1.15em;
  line-height: normal;
}
.mandatory-explanation {
  font-size: 0.86em;
  font-style: italic;
  text-align: right;
}
@media @small-screen {
  .mandatory-explanation {
    text-align:left;
  }
}

.form-control.invalid {
  border: solid 1px #BB0000;
  border: solid 1px rgba(255,0,0,0.5);
  &:focus {
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0px 0px 8px rgba(255,0,0,0.6);
  }
}


