//
// Bootstrap variable overrides
// --------------------------------------------------

// Import Bootstrap variables to be used in here
// Don't worry about circular references
@import (reference) '../bootstrap/variables.less';

// Disable responsive scaling
// Disable offcanvas.less -> body -> overflow-x: hidden; when these are enabled
@screen-xs: 2px;
@screen-sm: (@screen-xs + 1);
@screen-md: (@screen-sm + 1);
@screen-lg: (@screen-md + 1);

// change the grid to have 18 columns
// @grid-columns: 18;

// container width override
@container-desktop:            ((982px + @grid-gutter-width));
@container-large-desktop:      @container-desktop;

// remove rounded corners
@border-radius-base:  0;
@border-radius-large: 0;
@border-radius-small: 0;

// set the body background
@body-bg: #f4f4f4;

// reduce the height of the navbar
// and stop pushing things down
@navbar-height: 40px;
@navbar-margin-bottom: 0;


// media settings
@small-screen: ~"only screen and (max-width: @{screen-xs-max})";
@big-screen:   ~"only screen and (min-width: @{screen-xs})";
@highdensity:  ~"only screen and (-webkit-min-device-pixel-ratio: 1.5)",
               ~"only screen and (min--moz-device-pixel-ratio: 1.5)",
               ~"only screen and (-o-min-device-pixel-ratio: 3/2)",
               ~"only screen and (min-device-pixel-ratio: 1.5)";

