/* Mobile
-------------------------- */
@media @small-screen {
  .callout .callout-line {
      font-size: 12px !important;
      /*font-weight: bold;*/
  }  
}

/* Desktop
-------------------------- */
.callout {
  display:table;
  height: 60px;
  width: 60px;
  border-radius: 60px;
  font-size: 12px;
  text-transform: uppercase;
  position: absolute;
  top: 0;
  right: 0;
  margin: 15px;
  overflow:hidden;
  text-overflow:ellipsis;
  z-index: @zindex-callout;
  background: @tcom-default-light;

  &.callout-dark { background: @tcom-default; }

  &.callout-inline { position: relative; }

  // Color
  &,
  .base-blue &,
  .base-orange &,
  .base-green &,
  .base-magenta & {
    .lt-ie9 &, 
    .lt-ie9 &.callout-dark { background: url(../img/telstra/circle-sprite-ie8.png) no-repeat; }
  }

  &,
  .base-blue & {
      background: @tcom-blue-light;
      &.callout-dark { background: @tcom-blue; }
      .lt-ie9 & { background-position: 0 0; }
      .lt-ie9 &.callout-dark { background-position: -60px 0; }
  }
  .base-magenta & {
      background: @tcom-magenta-light;
      &.callout-dark { background: @tcom-magenta; }
      .lt-ie9 & { background-position: -180px 0; }
      .lt-ie9 &.callout-dark { background-position: -120px 0; }
  }
  .base-green & {
      background: @tcom-green-light;
      &.callout-dark { background: @tcom-green; }
      .lt-ie9 & { background-position: -300px 0; }
      .lt-ie9 &.callout-dark { background-position: -240px 0; }
  }
  .base-orange & {
      background: @tcom-orange-light;
      &.callout-dark { background: @tcom-orange; }
      .lt-ie9 & { background-position: -420px 0; }
      .lt-ie9 &.callout-dark { background-position: -360px 0; }
  }

  // callout text elements
  .callout-line {
    display:table-cell;
    vertical-align:middle;
    text-align: center;
    color: @tcom-white;
    line-height: normal; 
    top: 22px;
    font-size:10px;
  }

}