//
// Button Styles
// --------------------------------------------------

/**
 * TODO:
 *  - move variables to the variables file
 *  - make mixins for different button types
 *
 */
@tcom-button-height: 44px;
@tcom-button-chevron-size: 24px;

.button, .btn {
  //
  // default button styles
  padding: 14px 25px;
  border: none;
  text-decoration: none;
  display: inline-block;
  white-space: nowrap;
  margin: 0 @tcom-button-height 0 0;
  position: relative;
  outline: none;
  line-height: normal;

  max-width: 100%;

  @media @small-screen {
    margin-bottom: @tcom-global-content-spacing;
  }

  // Switch to inline for IE7 and below
  .lt-ie8 & {
    zoom: 1;
    display: inline;
  }

  &:hover,
  &:focus {
    text-decoration: none;
  }

  &:focus,
  &:focus:after {
    outline: thin dotted;
    outline: 5px auto -webkit-focus-ring-color;
    outline-offset: -2px;
  }

  &.active,
  &:active {
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: 0;

    &:focus,
    &:focus:after {
      outline: 0;
    }

    &:after {
      -webkit-box-shadow: none;
      box-shadow: none;
      outline: 0;
    }
  }

  &:after {
    .td-icon-family(icon-ui-right-arrow);
    width: @tcom-button-height;
    height: 100%; // uses container height from .btn, fixes issues in ie/ff
    position: absolute;

    right: -1 * (@tcom-button-height + 1);
    top: 0px;
    display: inline-block;
    text-align: center; //ie8 chevron position fix

    font-size: 24px;
    font-style: normal;
    font-weight: normal;
    line-height: @tcom-button-height;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  //
  // Square button size
  &.square {
    .square(52px);
    text-align: center;
    padding: 0;
    transition: background-color 250ms;

    .lt-ie9 & {
      margin-right: 52px;
    }

    > .td-icon {
      color: #fff !important; // This has to be important because default colour is !important in td-icons.css
      .font-size(24, 52);
    }

    &.loading {
      background-image: url(../img/telstra/ajax-loader-big.gif);
      background-repeat: no-repeat;
      background-position: 50% 50%;
      
      .td-icon {
        text-indent: -9999px;
      }
    }
  }

  &.white {
    background-color: #fff;
    .with-tcom-theme({
      color: @secondary;
    });

    &:after {
      background-color: #fff;
    }

    &:hover,
    &:focus,
    &:hover:after,
    &:focus:after {
      .with-tcom-theme({
        color: @primary;
      });
    }

    &:active,
    &:active:after {
      color: #fff;

      .with-tcom-theme({
        color: darken(@secondary, 20%);
      });
    }
  }

  //
  // Gravur variation
  &.gravur {
    font-size: 16px !important;
    line-height: 1;
  }

  //
  // Secondary
  &.secondary {
    color: #4f4f4f !important;
    background-color: #dadada !important;

    &:hover,
    &:focus {
      color: #fff !important;
      background-color: #4f4f4f !important;

      &:after {
        color: #fff !important;
        background-color: #4f4f4f !important;
      }
    }

    &:focus,
    &:focus:after {
      outline: thin dotted;
      outline: 5px auto -webkit-focus-ring-color;
      outline-offset: -2px
    }

    &:active {
      color: #fff !important;
      background-color: #000 !important;
      outline: 0;

      &:after {
        color: #fff !important;
        background-color: #000 !important;
        outline: 0;
      }
    }

    &:after {
      color: #4f4f4f !important;
      background-color: #dadada !important;
    }
  }

  &.dropdown-toggle,
  &.chevron-down {
    margin-right: 0;

    &:after {
      .td-icon-family(icon-ui-down-arrow);
      right: 0;
      background-color: transparent;
      outline: 0;

      &:focus {
        outline: 0;
      }
    }
  }

  //
  // Plain button override
  &.plain,
  &.chevron,
  &.square {
    margin-right: 0;

    &:after {
      display: none;
      content: '';
      width: 0;
      height: 0;
    }
  }

  //
  // Plain button override
  &.chevron {
    font-size: 24px;
    padding: 8px 10px;

    &:before {
      .td-icon-family(icon-ui-right-arrow);
    }
  }

  &.loading:after {
    background: url(../img/telstra/ajax-loader-big.gif) no-repeat 50% 50%;
    content: '';
  }

  //
  // theme colors
  // --------------------------------------------------

  .base-default & {
    .base-theme-buttons(@tcom-default;
                        @tcom-default-light;
                        @tcom-default-dark;);
  }






  //
  // Base Grey Color
  .base-grey & {
    .base-theme-buttons(@tcom-grey;
                        @tcom-grey-light;
                        @tcom-grey-dark;);
  }



  //
  // Base Orange Color
  .base-orange & {
    .base-theme-buttons(@tcom-orange;
                        @tcom-orange-light;
                        @tcom-orange-dark;);
  }

  //
  // Base Green Color
  .base-green & {
    .base-theme-buttons(@tcom-green;
                        @tcom-green-light;
                        @tcom-green-dark;);
  }

  //
  // Base Turquoise Color
  .base-turquoise & {
    .base-theme-buttons(@tcom-turquoise;
                        @tcom-turquoise-light;
                        @tcom-turquoise-dark;);
  }

  //
  // Base Blue Color
  .base-blue & {
    .base-theme-buttons(@tcom-blue;
                        @tcom-blue-light;
                        @tcom-blue-dark;);
  }

  //
  // Base Purple Color
  .base-purple & {
    .base-theme-buttons(@tcom-purple;
                        @tcom-purple-light;
                        @tcom-purple-dark;);
  }

  //
  // Base Magenta Color
  .base-magenta & {
    .base-theme-buttons(@tcom-magenta;
                        @tcom-magenta-light;
                        @tcom-magenta-dark;);
  }
}

.close {
  float: right;
  display: block;
  .generate-theme(s; background-color, default);
  height: @close-btn-size;
  width: @close-btn-size;
  border: 0;
  padding: 0;
  cursor: pointer;

  &:hover,
  &:focus {
    .generate-theme(s; background-color, dark);
  }

  &:after {
    .td-icon-family(icon-ui-cross);
    font-size: 18px !important;
    color: @tcom-white !important;
    font-weight: normal !important;
    line-height: @close-btn-size;
  }
}

//
// Radio buttons and checkboxes
[data-toggle="buttons"] .btn {
  color: @tcom-grey-dark;
  background-color: @tcom-grey-lighter;

  &:hover,
  &:focus {
    color: @tcom-white;
    background-color: @tcom-grey-dark;
  }
  &:focus {
    outline: thin dotted;
    outline: 5px auto -webkit-focus-ring-color;
    outline-offset: -2px;
  }
  &.active {
    color: @tcom-light-on-dark-color;
    .generate-theme(s; background-color, dark);
  }

  /* Override bootstrap styles which hide input elements */
  >input[type="radio"],
  >input[type="checkbox"] {
    display: inline-block !important;
    position: absolute;
    left: -99999px;
  }
}

/* ==========================================================================
 * - SEARCH
 * ========================================================================== */

.btn-search {
  margin: 0;
  transition: background-color 250ms;
  .generate-theme(s; background-color, dark);

  &:hover,
  &:focus,
  &:active {
    .generate-theme(s; background-color, darken, dark, 10%);
  }
}
