// adapted from: https://raw.githubusercontent.com/noraesae/perfect-scrollbar/master/src/perfect-scrollbar.scss

// Scrollbar mixins
.perfect-scrollbar-rail-default {
  display: none;
  position: absolute; /* please don't change 'position' */
  border-radius: 4px;
  opacity: 0;
  transition-property: background-color, opacity;
  transition-duration: 200ms;
  transition-timing-function: linear;
  z-index: @zindex-scrollup;
}

.perfect-scrollbar-rail-hover {
  background-color: rgba(255, 255, 255, 0.1);
  opacity: 0.9;
}

.perfect-scrollbar-default {
  position: absolute; /* please don't change 'position' */
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 4px;
  transition-property: background-color;
  transition-duration: 200ms;
  transition-timing-function: linear;
}

.perfect-scrollbar-hover {
  background-color: rgba(255, 255, 255, 1);
}

.perfect-scrollbar-in-scrolling {
  &.in-scrolling {
    .perfect-scrollbar-rail-hover();
  }
}

.ps-container {
  &.ps-active-x > .ps-scrollbar-x-rail,
  &.ps-active-y > .ps-scrollbar-y-rail {
    display: block;
  }

  & > .ps-scrollbar-x-rail {
    .perfect-scrollbar-rail-default();
    bottom: 3px; /* there must be 'bottom' for ps-scrollbar-x-rail */
    height: 8px;

    & > .ps-scrollbar-x {
      .perfect-scrollbar-default();
      bottom: 0; /* there must be 'bottom' for ps-scrollbar-x */
      height: 8px;
    }

    .perfect-scrollbar-in-scrolling();
  }

  & > .ps-scrollbar-y-rail {
    .perfect-scrollbar-rail-default();
    right: 8px; /* there must be 'right' for ps-scrollbar-y-rail */
    width: 8px;

    & > .ps-scrollbar-y {
      .perfect-scrollbar-default();
      right: 0; /* there must be 'right' for ps-scrollbar-y */
      width: 8px;
    }

    .perfect-scrollbar-in-scrolling();
  }

  &:hover {
    & > .ps-scrollbar-x-rail,
    & > .ps-scrollbar-y-rail {
      opacity: 0;
      .perfect-scrollbar-in-scrolling();
    }

    & > .ps-scrollbar-x-rail:hover {
      .perfect-scrollbar-rail-hover();

      & > .ps-scrollbar-x {
        .perfect-scrollbar-hover();
      }
    }

    & > .ps-scrollbar-y-rail:hover {
      .perfect-scrollbar-rail-hover();

      & > .ps-scrollbar-y {
        .perfect-scrollbar-hover();
      }
    }
  }
}
