.switch-nav {
	a {
		color: @tcom-white;
		.font-size(16);
		display: block;
		padding:17px 0 17px 0;

		&:hover, &.active {
			text-decoration: none;
			background-color: @tcom-white;
			color: @tcom-grey;


			.td-icon-sm, .td-icon-xs {
				color: @tcom-grey !important;
			}
		}

		.td-icon-sm, .td-icon-xs {
			color: @tcom-white !important;
			top: -1px;
		}
	}

/*
This is a ugly fix for SAL-1018

Different browser handle pixels in float values differently
Chrome considers floor values wheareas other browser considers ceil value on rendering

Due to this we see a 1px line on the right for card SAL-1018.

To fix this we provide a fix width to container which will work well for tabs in count between 2-4
*/
@media @big-screen {
	&.standard.row{
		width: 984px;
	}
}

  @media @small-screen {
    a {
    	text-align: left;
    	padding-left: 15px;

    	.td-icon-sm {
    		display: none;
    	}
    	.td-icon-xs {
    		float: right;
    		padding-right:15px;
    	}
    }
  }
}