//
// Global navigation
// --------------------------------------------------

@tcom-globalnav-height: 44px;

#global-nav {
	border-color: @tcom-globalnav-bg;
	background: url("../img/telstra/global-nav-bg.png") repeat-x 0 0 @tcom-globalnav-bg;
	border: 0;

  @media @small-screen {
    display: none;
  }

  .navbar-collapse {
    padding: 0;
  }

  &.navbar-inverse .navbar-nav > li > a {
    color: @tcom-text-light;
    border-left: 1px solid @tcom-globalnav-border;

    &:hover,
    &:focus {
      background: @tcom-globalnav-highlight;
    }
  }

  &.navbar-inverse .navbar-nav > li:last-of-type > a {
    border-right: 1px solid @tcom-globalnav-border;
  }

  &.navbar-inverse .navbar-nav > .active > a,
  &.navbar-inverse .navbar-nav > .active > a:hover,
  &.navbar-inverse .navbar-nav > .active > a:focus {
    background: @tcom-globalnav-highlight;
    font-weight: bold;
  }

  // mobile header
  .navbar-brand {
    padding: 7px;
    margin-left: 5px;
    .site-logo {
      height: 30px;
    }
  }

  .navbar-toggle {
    .noselect();
    border: 0;
    background: @tcom-grey-lighter;
    margin: 0;
    padding: 10px;
    height: @tcom-globalnav-height;

    &.pull-left {
      padding: 13px 10px;
    }

    .icon-search-find {
      font-size: 24px;
      color: @tcom-grey !important;
    }

    .icon-bar {
      height: 3px;
      width: 24px;
      border-raidus: 5px;
      background-color: @tcom-grey;

      + .icon-bar {
        margin-top: 3px;
      }
    }

    &:active {
      background-color: @tcom-grey;
      .icon-bar {
        background: @tcom-grey-lighter;
      }
      .td-icon {
        color: @tcom-grey-lighter !important;
      }

    }
  }
}

// hiding global nav
.hide-global-nav {
  #spectrum {
    margin-top: @tcom-globalnav-height;
  }

  #global-nav {
    display: none;
  }
}
