//
// Standard row
// --------------------------------------------------
// 
// Column distribution (and their grid representation):
// 1: 100%        (12)
// 2: 50-50%      (6-6)
//    33-66%      (4-8)
//    66-33%      (8-4)
// 3: 33-33-33%     (4-4-4)
// 4: 25-25-25-25%    (3-3-3-3)
// 
// Nesting support (and their grid representation):
// 100% (12)
//    50-50     (6-6)
//    33-33-33    (4-4-4)
//    33-66     (4-8)
//    66-33     (8-4)
//    25-25-25-25   (3-3-3-3)
//    
// 66% (8)
//    50-50     (4-4)
//    33-66     x
//    66-33     x
//    
// 50% (6)
//    50-50     (3-3)
//    33-33-33    (2-2-2)
//    33-66     (2-4)
//    66-33     (4-2)
//    
// 33% (4)
//    50-50     (2-2)
//    33-66     x
//    66-33     x
//    
// 25% (3)
//    None supported
//

// symmetric styles
.col-100             > .col  {.make-md-column(12)}
.col-50-50           > .col,
.col-100x50-50       > .col  {.make-md-column(6)}
.col-33-33-33        > .col,
.col-100x33-33-33    > .col  {.make-md-column(4)}
.col-25-25-25-25     > .col,
.col-100x25-25-25-25 > .col  {.make-md-column(3)}

// 33-66% columns
.col-33-66-c0,
.col-66-33-c1,
.col-100x33-66-c0,
.col-100x66-33-c1            {.make-md-column(4)} // Note: TEL-581 issue with flex

// 66-33% columns
.col-66-33-c0,
.col-33-66-c1,
.col-100x66-33-c0,
.col-100x33-66-c1            {.make-md-column(8)} // Note: TEL-581 issue with flex

// 25-75% columns
.col-25-75-c0,
.col-75-25-c1,
.col-100x25-75-c0,
.col-100x25-75-c1            {.make-md-column(3)}

// 75-25% columns
.col-75-25-c0,
.col-25-75-c1,
.col-100x75-25-c0,
.col-100x25-75-c1            {.make-md-column(9)}

// Nested variations
// --------------------------------------------------

// 100(50-50)%
// These nesting variations are mixed with the 100% non-nesting style definitions

// 50(33-33-33)-50%
.col-50-50x33-33-33-c0,
.col-50-50x33-33-33-c1,
.col-50-50x33-33-33-c2 {.make-md-column(2)}
.col-50-50x33-33-33-c3 {.make-md-column(6)}

// 50-50(33-33-33)%
.col-50-50xx33-33-33-c0 {.make-md-column(6)}
.col-50-50xx33-33-33-c1,
.col-50-50xx33-33-33-c2,
.col-50-50xx33-33-33-c3 {.make-md-column(2)}

// 50(33-33-33)-50(33-33-33)%
.col-50-50x33-33-33x33-33-33 > .col {.make-md-column(2)}

// 50(33-66)-50%
.col-50-50x33-66-c0 {.make-md-column(2)}
.col-50-50x33-66-c1 {.make-md-column(4)}
.col-50-50x33-66-c2 {.make-md-column(6)}

// 50(66-33)-50%
.col-50-50x66-33-c0 {.make-md-column(4)}
.col-50-50x66-33-c1 {.make-md-column(2)}
.col-50-50x66-33-c2 {.make-md-column(6)}

// 50-50(33-66)%
.col-50-50xx33-66-c0 {.make-md-column(6)}
.col-50-50xx33-66-c1 {.make-md-column(2)}
.col-50-50xx33-66-c2 {.make-md-column(4)}

// 50-50(66-33)%
.col-50-50xx66-33-c0 {.make-md-column(6)}
.col-50-50xx66-33-c1 {.make-md-column(4)}
.col-50-50xx66-33-c2 {.make-md-column(2)}

// 50(33-66)-50(33-66)%
.col-50-50x33-66x33-66-c0 {.make-md-column(2)}
.col-50-50x33-66x33-66-c1 {.make-md-column(4)}
.col-50-50x33-66x33-66-c2 {.make-md-column(2)}
.col-50-50x33-66x33-66-c3 {.make-md-column(4)}

// 50(66-33)-50(66-33)%
.col-50-50x66-33x66-33-c0 {.make-md-column(4)}
.col-50-50x66-33x66-33-c1 {.make-md-column(2)}
.col-50-50x66-33x66-33-c2 {.make-md-column(4)}
.col-50-50x66-33x66-33-c3 {.make-md-column(2)}

// 50(33-66)-50(66-33)%
.col-50-50x33-66x66-33-c0 {.make-md-column(2)}
.col-50-50x33-66x66-33-c1 {.make-md-column(4)}
.col-50-50x33-66x66-33-c2 {.make-md-column(4)}
.col-50-50x33-66x66-33-c3 {.make-md-column(2)}

// 50(66-33)-50(33-66)%
.col-50-50x66-33x33-66-c0 {.make-md-column(4)}
.col-50-50x66-33x33-66-c1 {.make-md-column(2)}
.col-50-50x66-33x33-66-c2 {.make-md-column(2)}
.col-50-50x66-33x33-66-c3 {.make-md-column(4)}

// 50(50-50)-50%
.col-50-50x50-50-c0,
.col-50-50x50-50-c1 {.make-md-column(3)}
.col-50-50x50-50-c2 {.make-md-column(6)}

// 50-50(50-50)%
.col-50-50xx50-50-c0 {.make-md-column(6)}
.col-50-50xx50-50-c1,
.col-50-50xx50-50-c2 {.make-md-column(3)}

// 50(50-50)-50(50-50)%
.col-50-50x50-50x50-50 > .col {.make-md-column(3)}

// 66(50-50)-33%
.col-66-33x50-50-c0,
.col-66-33x50-50-c1,
.col-66-33x50-50-c2 {.make-md-column(4)}

// 66-33(50-50)%
.col-66-33xx50-50-c0 {.make-md-column(8)}
.col-66-33xx50-50-c1,
.col-66-33xx50-50-c2 {.make-md-column(2)}

// 33(50-50)-66%
.col-33-66x50-50-c0,
.col-33-66x50-50-c1 {.make-md-column(2)}
.col-33-66x50-50-c2 {.make-md-column(8)}

// 33-66(50-50)%
.col-33-66xx50-50-c0,
.col-33-66xx50-50-c1,
.col-33-66xx50-50-c2 {.make-md-column(4)}

// 66(50-50)-33(50-50)
// 33(50-50)-66(50-50)
// these are rarely used
.col-33-66x50-50x50-50-c0,
.col-33-66x50-50x50-50-c1,
.col-66-33x50-50x50-50-c2,
.col-66-33x50-50x50-50-c3 {.make-md-column(2)}
.col-66-33x50-50x50-50-c0,
.col-66-33x50-50x50-50-c1,
.col-33-66x50-50x50-50-c2,
.col-33-66x50-50x50-50-c3 {.make-md-column(4)}




.standard.row {
  position: relative;

  // No background image on mobile
  // --------------------------------------------------
  // @media @small-screen {
  //   background-image: none !important;
  // }

  // Row transparcy
  // --------------------------------------------------
  &.transparent {
    background-color: transparent;
    .box-shadow(0 0 0 rgba(0, 0, 0, 0));
    border: 0 none;

    // prevent content from touching the top of the col-wrapper when row title is transparent
    &.transparent + .standard.row.content .col-wrapper {
      padding-top: @tcom-global-content-padding;
    }
  }

  // Row level title styles
  // --------------------------------------------------
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
