  //    &:extend(span.sub-heading);
  //    
  //    margin-top: @line-height-computed / 2 !important;
  //    margin-bottom: @line-height-computed / 2 !important;
  //  }
  //  REMOVE ABOVE AFTER TESTING HEADING STYLES

  &.fine-print {
      // &:extend(.fine-print);
      line-height: 18px;
      font-weight: bold;
      //margin-bottom: 3px !important;
    }

  //  &.gravur {
  //    font-size: 30px;
