}

  // Row title styles
  // --------------------------------------------------

  .v11 &.title {

    &.row-border {
      margin-right: -@tcom-col-content-margin;
      margin-left: -@tcom-col-content-margin;
     
    }

  }    

  &.title {
    &:extend(.panel);
    .box-shadow(0 0 0 rgba(0, 0, 0, 0));
    border: 0;
    margin-bottom:0;

    [class ^= 'col-'] {
      padding: @tcom-col-content-padding;
    }

    &.no-content {
      margin-bottom: @line-height-computed;

      &.transparent {
        margin-bottom: 0;

      }

      &.no-padding {
        margin-bottom: 0;
      }
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      // moved to .standard.row
      // margin-top: @line-height-computed / 2 !important;
      // margin-bottom: @line-height-computed / 2 !important;
      margin: 0;
      padding-top: 0;
      padding-bottom: 0;
    }

    &.transparent:extend(& .transparent) {
      // inherit from the row (parent) transparency
    }

    &.no-padding {
      [class ^= 'col-'] {
        padding: 0;
        padding-bottom: @tcom-col-content-padding;
      }
    }

    // DEPRECATED
    // &.separator {
    //   border-bottom: 1px solid @tcom-content-border;
    // }

    &.row-border {
      border-top: 1px solid @tcom-content-border;
      border-right: 1px solid @tcom-content-border;
      border-left: 1px solid @tcom-content-border;

      &.no-content {
        border-bottom: 1px solid @tcom-content-border;
      }

        
    }
    

    // Remove the unecessary padding for the col-wrapper
    & + .row.content{

      .col-wrapper{
        padding-top: 0;

        @media @small-screen {
          padding-top: @tcom-col-content-padding;
        }
      }

      &.no-title,  &.col-borders {
        .col-wrapper{
          padding-top: @tcom-col-content-padding;
        }

      }
    }

    & + .row.content.margin .col{
      margin-top:0;
    }
  }

  // Row content styles
  // --------------------------------------------------

  .v11 &.content {
    &.row-border {
      margin-right: -@tcom-col-content-margin;
      margin-left: -@tcom-col-content-margin;
    }

  } 


  &.content {
    &:extend(.panel);
    .box-shadow(0 0 0 rgba(0, 0, 0, 0));
    border: 0;

    .col {
      // remove the padding from column
      padding: 0;

      &.first .col-wrapper {
           margin-left: 0;
      }

      &.last .col-wrapper {
         margin-right: 0;
      }

      &.nest-start .col-wrapper {
        padding-right: 0;
        margin-right: @tcom-col-nesting-gutter;
      }

      &.nest-middle .col-wrapper {
        padding-left: 0;
        padding-right: 0;
        margin-left: @tcom-col-nesting-gutter;
        margin-right: @tcom-col-nesting-gutter;
      }

      &.nest-end .col-wrapper {
        padding-left: 0;
        margin-left: @tcom-col-nesting-gutter;
      }

      .col-wrapper {
        // apply the pading to the wrapper
        padding:  @tcom-col-content-padding;
        //margin-left: @tcom-col-gutter;
        //margin-right: @tcom-col-gutter;
        
        // eliminates extra spacing for nested rows (tabs implemented in AEM)
        .col-wrapper {
          padding-bottom: 0 !important;
        }
        .row {
          margin-bottom: 0 !important;

          &.content {
            .switchmodule & {
              margin-bottom: @tcom-col-content-margin !important;
            }
          }
          //TEL-2210 - Switch module column layout 
          &.group-start,
          &.group-middle {
            .switchmodule & {
              margin-bottom: 0 !important; 
            }
          }
        }
      }
    }

    &.margin {
        .col {
          margin-top: @tcom-col-content-margin;
          margin-bottom: @tcom-col-content-margin;
        }
      }

    &.col-borders {
      .col {
        //margin-top: @tcom-content-padding;
        //margin-bottom: @tcom-content-padding;

        .col-wrapper {
          border: 1px solid @tcom-content-border;
          margin-left:  @tcom-col-content-padding;
          margin-right:  @tcom-col-content-padding;

          @media @small-screen {
            & {
              margin: 0;
            }
          }
        }
      }
    }

    &.transparent.col-borders:extend(& .transparent) {
      // inherit from the parent transparency
      
      .col {
        .col-wrapper {
          background-color: @panel-bg;
        }
      }
    };

    // previously applied to the individual column
    // now being applied to the entire row
    // the style gets cascaded
    &.separators {
      .col {
        border-left: 1px solid @tcom-content-border;

        &:first-child {
          border-left: 0 none;
        }
      }

      

      // switch off seperators if borders are enabled; but...
      &.col-borders {
        .col {
          border-left: 0 none;
        }
      }

      @media @small-screen {
        & .col {
          // @todo: need to fix here with a col-wrapper
          border-left: 0 none;
          border-top: 1px solid @tcom-content-border;

          &:first-child {
            border-top: 0 none;
          }

          &.padding {
            margin-top: 0; // TODO: replace with a variable
            margin-bottom: 0;
            margin-left: @tcom-content-padding;
            margin-right: @tcom-content-padding;
            padding-left: 0;
            padding-right: 0;
          }
        }
      }
    }

    // Row border styles
    // --------------------------------------------------
    &.row-border {
      border-right: 1px solid @tcom-content-border;
      border-bottom: 1px solid @tcom-content-border;
      border-left: 1px solid @tcom-content-border;

      &.no-title {
        border-top: 1px solid @tcom-content-border;
      }

      &.content {
        //margin-right: 0px;
      }      
    }

    // Row grouping
    // --------------------------------------------------
    &.group-start,
    &.group-middle {
      margin-bottom: 0;

      &.row-border {
        border-bottom: 0 none;
      }
    }

    &.group-middle {
      &.row-border {
        border-top: 0 none;
        border-bottom: 0 none;
      }
    }

    &.group-end {
      &.row-border {
        border-top: 0 none;
      }
    }

    &.group {
      &.row-ruler {
        .col .col-wrapper {
          // margin-top: 10px;
          margin-bottom: 0;
        }

        &.separators {
          .col .col-wrapper {
            margin-bottom: @tcom-col-content-margin;
          }
        }
      }

      .col .col-wrapper {
        //margin-top: 10px;
        margin-bottom:  @tcom-row-group-margin;
      }

      &.separator {
        // Note: this is deprecated. Will be reomved after all use of the old class has been made redundant.
        // border-bottom: 1px solid @tcom-content-border;
      }

      .ruler {
        float: none;
        clear: both;
        margin-bottom: @tcom-row-group-margin;
        .switchmodule & {
          top: 20px;  
        }

        &.no-padding {
          padding-left: 0;
          padding-right: 0;
          margin-bottom: 0;
        }

        .hr {
          background-color: @tcom-content-border;
          height: 1px;
        }
      }

    }

    //no content padding
    &.no-content-padding {
      .col-wrapper {
          margin: 0; 
          padding-bottom: 0;
          padding-right: @tcom-col-gutter;
          padding-left: @tcom-col-gutter;
      }
      .first {
        .col-wrapper {
          padding-left: @tcom-col-content-padding;
        }
      }
      .last {
        .col-wrapper {
          padding-right: @tcom-col-content-padding;
        }      
      }        
    }

    // no column padding
    &.no-col-padding {
      .col-wrapper {
        padding: 0;
      }

      .col-wrapper {
        padding: 0;
      }

      // and no column borders
      &.col-borders {
        margin-left: -(@tcom-col-content-margin + @tcom-col-gutter); // 25px
        margin-right: -(@tcom-col-content-margin + @tcom-col-gutter); // 25px

        .col {
          margin-top: 0;
          margin-bottom: 0;

          .col-wrapper {
            padding: @tcom-col-content-padding;
            
            margin-right: @tcom-col-gutter;
            margin-left: @tcom-col-gutter;
          }

          &:first-child {
            .col-wrapper {
              margin-left: @tcom-col-gutter;
            }
          }

          &:last-child {
            .col-wrapper {
              margin-right: @tcom-col-gutter;
            }
          }
        }

        // fixes the white border on left and right for this combination
        &.col-100 {
          // might need a .col-wrapper here too
          &:before,
          &:after {
            background: transparent;
          }
        }
      }
    }

    // Column styles
    // --------------------------------------------------
    .col {
      &.no-padding {
        padding-left: 0;
        padding-right: 0;
      }
    }

    // Column wrapper
    // --------------------------------------------------
    .col-wrapper {
      // Removed in favour of adding top padding to the entire content area
      // & > p:first-child {
      //   margin-top: 15px;
      // }

      & h1,
      & h2,
      & h3,
      & h4,
      & h5,
      & h6,
      & .panel-heading,
      & .sub-heading,
      & .content-heading,
      & .content-sub-heading {
        margin-top: 0;
      }

      // deprecated
      // &.boxed {
      //   border: 1px solid @tcom-content-border;
      //   padding: 15px;
      //   margin-bottom: 15px;
      // }

      // TODO: Deprecated
      &.padded {
        margin-top: 15px;
      }
    }

    i.fa {
      margin-right: 5px;
    }

    .col-image {
      margin-bottom: 20px;
    }

  }

  // Remove the first column border-left
  // TODO: tidy up
  & > .content.separator:first-child,
  .standard-title + .standard-content.separator {
    border-left: 0 none;
  }
}

// Support module styles
// --------------------------------------------------
.support {

  &.nav li {
    margin-right: 0;
    margin-left: 0;
  }

  &.support-4 li { width: 25%; }
  &.support-3 li { width: 33.333%; }
  &.support-2 li { width: 50%; }

  &.row {

    @media @small-screen {
      margin-top: 0;
      padding-top: @tcom-col-content-padding;
    }

    &.margin.content .col {
      margin-top: 0;

      .col-wrapper {
        padding-top: 0;
        //padding-bottom: 0;

        .hidden-xs.hidden-sm {
          //padding-top: @tcom-col-content-padding + (@tcom-col-gutter / 2);
          padding-bottom: @tcom-col-content-padding + (@tcom-col-gutter / 2);
        }
      }
    }

    //Margin top set on no title in Support module
    &.no-title.margin.content .col {
        margin-top: @tcom-col-content-padding;
    }    

    .col.tab-pane {
      display: block;
    }

    @media @small-screen {
      & {
        .col.tab-pane {
          display: none;

          &.active {
            display: block;
          }
        }

        &.content.separators {
          .col {
            border-top: 0 none;
          }
        }
      }
    }
  }
}

// Disclaimer row styles
// --------------------------------------------------
.standard.row.content {
  &.no-title{
    .col{
      @media @small-screen {
        // Commented out to fix TEL-2812
        // margin-bottom: @tcom-col-content-margin;
      }
    }
  }
  &.col-borders {
    .col {
      // .col-wrapper {
      //   border: 1px solid @tcom-content-border;
      //   // margin: 10px;
      //   
      //   @media (max-width: @screen-sm-max) {
      //     & {
      //       margin: 0;
      //     }
      //   }
      // }

      // TODO: need to identify a pattern in following
      // nesting styles. Styling needs to be pragmatic
      // NEEDS OPTIMISATION

      .col-wrapper {
        margin-right: @tcom-col-gutter;
        margin-left: @tcom-col-gutter;
      }

      &.first{
        .col-wrapper {
          margin-left: @tcom-col-content-margin;
          @media @small-screen {
            margin-left: @tcom-global-content-spacing;
          }
        }
      }

      &.last {
        .col-wrapper {
          margin-right: @tcom-col-content-margin;
          @media @small-screen {
            margin-right: @tcom-global-content-spacing;
          }
        }
      }

      &.nest-start {
        .col-wrapper {
          border-right: 0 none;
          margin-right: 0;
          padding-right: @tcom-col-content-padding;

          @media @small-screen { 
            margin-left: @tcom-global-content-spacing;
            margin-right: @tcom-global-content-spacing;
            border: 1px solid @tcom-grey-lighter;
          }
        }

        @media @small-screen {
          border-top: 0px none;
          margin-bottom: 0;
        }

        ~ .last {
          @media @small-screen {
            border-top: 0px none;
          }
        }
      }

      &.nest-middle {
        .col-wrapper {
          border-left: 0 none;
          border-right: 0 none;
          margin-left: 0;
          margin-right: 0;
          padding-left: @tcom-col-content-padding;
          padding-right: @tcom-col-content-padding;

          @media @small-screen { 
            margin-left: @tcom-global-content-spacing;
            margin-right: @tcom-global-content-spacing;
            border: 1px solid @tcom-grey-lighter;
            border-top: 0 none;
          }
        }

        @media @small-screen {
