// ensures everything in the header sits above the shade
header {
  position: relative;
  z-index: @zindex-megamenu;

  &.hide-header{
    margin-bottom: @tcom-row-group-margin;

    + .fullwidth-container {
      margin-top: -@tcom-row-group-margin;
      margin-bottom: @tcom-row-group-margin;
    }

    @media @small-screen {
      margin-bottom: 0;

      + .fullwidth-container {
        margin: 0;
      }
    }
  }

  @media @small-screen {
    position: static;
  }
}
