//
// HTML
// --------------------------------------------------

// Mobile font issue fix
// @ref: https://jira.ae.sda.corp.telstra.com/browse/TEL-2383
// --------------------------------------------------
html * {
  max-height: 1000000px;
}

// Styles to give the illusion the footer extends to the bottom of the screen in high resolution monitors
html {
  height: 100%;
  background: #555;
}

body,
html,
#spectrum {
  min-width: 1024px;

  @media @small-screen {
    min-width: 0;

    &.fixed {
      position: fixed;
      backface-visibility: hidden;
    }
  }
}
body.white-bkg {
  background: #fff;
}
//
// Body Spectrum
// --------------------------------------------------
#spectrum {
  position: absolute;
  display: block;
  width: 100%;
  height: 562px;
  max-height: 562px;
  margin-top: 80px; //height of the global nav + the primary nav
  background-position: 50% bottom;
  background-repeat: no-repeat;
  background-image: url(../img/telstra/v2.1-spectrum-blue.png);
  visibility: hidden;

  @media @small-screen {
    margin-top: 0;
    .tcom-theme-bg-vertical-start-end-70;
  }

  .base-blue & {
    background-image: url(../img/telstra/v2.1-spectrum-blue.png);
  }

  .base-green & {
    background-image: url(../img/telstra/v2.1-spectrum-green.png);
  }

  .base-magenta & {
    background-image: url(../img/telstra/v2.1-spectrum-magenta.png);
  }

  .base-orange & {
    background-image: url(../img/telstra/v2.1-spectrum-orange.png);
  }

  &.spectrum-page-takeover {
    background-position: 50% 0;
  }
}

//
// Body
// --------------------------------------------------
.fullwidth-container {
  position: relative;
}

.container.main-content {
  padding: 0 30px;
  margin-bottom: 40px;
  min-height: 500px;
  position: relative;

  @media @small-screen{
    padding:0 @tcom-global-content-padding;
  }
}

// v11
.main-content.v11 {
  & > .row {
    background: #fff;

    > .col-sm-2:first-child {
      padding-right: 5px;

      @media @small-screen {
        display: none;
      }
    }
  }

}

.v11 .page-panel-heading {
  &:extend(span.panel-heading);
  margin-top:0 !important;
  margin-bottom:10px !important;
}

.v11 {

  .page-content {
    padding-top: @tcom-global-content-spacing * 2;
    padding-bottom: @tcom-global-content-spacing * 2;

    // with LHN
    &.col-sm-10 {
      padding-right: @tcom-global-content-spacing + @tcom-global-content-padding;
      padding-left: @tcom-global-content-spacing * 2;
    }

    // without LHN
    &.col-sm-12 {
      padding-right: @tcom-global-content-spacing + @tcom-global-content-padding;
      padding-left: @tcom-global-content-spacing + @tcom-global-content-padding;
    }
  }
}


//
// Common Styles
// --------------------------------------------------

// gravur font-size should be 1.25 times larger than regular font
.gravur {
  .font-gravur();
  font-weight: normal !important;
  font-style: normal !important;
  line-height: normal !important;
}

.gravur-bold {
  .font-gravur();
  font-weight: bold;
}

.tight-spacing() {
  padding: 0;
  margin: 0;
}

.h-padding() {
  padding-left: 10px;
  padding-right: 10px;
}

.v-padding() {
  padding-top: 10px;
  padding-bottom: 10px;
}

// Generic hyperlink colours
// --------------------------------------------------
a {
    color: @tcom-default-dark;
    .base-blue & { color: @tcom-blue-dark; }
    .base-magenta & { color: @tcom-magenta-dark; }
    .base-orange & { color: @tcom-orange-dark; }
    .base-green & { color: @tcom-green-dark; }

    &.content-link {
      display: inline-block;
      position: relative;
      margin-left: 10px;

      &:before {
        .td-icon-family(icon-ui-right-arrow);
        position: absolute;
        margin-left: -14px;
      }
    }

    &[class^="td-icon"]:hover,
    &[class^="td-icon"]:focus {
      text-decoration: none;
    }
}

//== Typography
//

// Generic text colours
// --------------------------------------------------
@font-face {
  font-family: "Gravur";
  src: ~"url('@{font-path}gravur.eot')"; /* IE9 Compat Modes */
  src: ~"url('@{font-path}gravur.eot?#iefix') format('embedded-opentype')", /* IE6-IE8 */
       ~"url('@{font-path}gravur.woff') format('woff')", /* Modern Browsers */
       ~"url('@{font-path}gravur.ttf')  format('truetype')", /* Safari, Android, iOS */
       ~"url('@{font-path}gravur.svg#svgFontName') format('svg')"; /* Legacy iOS */
}

.text-light {
  color: @tcom-text-light !important;
}

.text-grey {
  color: @tcom-text-grey !important;
}

.text-dark {
  color: @tcom-text-dark !important;
}

.text-theme {
  color: @tcom-default !important;
  .base-blue & { color: @tcom-blue !important; }
  .base-green & { color: @tcom-green !important; }
  .base-orange & { color: @tcom-orange !important; }
  .base-magenta & { color: @tcom-magenta !important; }
}

.text-theme-light,
.light-colour {
  color: @tcom-default-light !important;
  .base-blue & { color: @tcom-blue-light !important; }
  .base-green & { color: @tcom-green-light !important; }
  .base-orange & { color: @tcom-orange-light !important; }
  .base-magenta & { color: @tcom-magenta-light !important; }
}

// the reason for colour and theme to be here insteard of
// being with the default colour is because that's how it
// was done in the brand 2.1 refresh for t.com
.text-theme-dark,
.dark-colour,
.colour,
.theme {
  color: @tcom-default-dark;
  .base-blue & { color: @tcom-blue-dark !important; }
  .base-green & { color: @tcom-green-dark !important; }
  .base-orange & { color: @tcom-orange-dark !important; }
  .base-magenta & { color: @tcom-magenta-dark !important; }
}

// Special paragraph styles
// --------------------------------------------------
span.feature,
p.feature {
  font-size: 16px !important;
}

span.standard,
p.standard {
  font-size: 14px !important;
}

span.small,
p.small {
  font-size: 12px !important;
}

.underline {
  border-bottom: 1px solid @tcom-content-border;
  padding-bottom: 5px;
}

.strong {
  font-weight: bold;
}

// Content heading styles
// --------------------------------------------------
.heading-classes() {
  &.panel-heading {
    font-size: 30px;
    line-height: 45px;
    .tight-spacing();
  }

  &.sub-heading {
    font-size: 24px;
    line-height: 36px;
    .tight-spacing();

    &.gravur {
      font-size: 30px;
      // line-height should be inherited from the global .gravur class

      &.theme {
        &:extend(.theme);
      }
    }

    &.sales-number {
      text-align: right;

      @media @small-screen {
         text-align: center;
      }
    }
  }

  &.content-heading {
    font-size: 20px;
    font-weight: bold;
    line-height: 30px;
    .tight-spacing();

    .v11 & {
      font-size: 18px;
    }
  }

  &.content-sub-heading {
    font-size: 16px;
    line-height: 1.5;
    .tight-spacing();

    &.small,
    /* &-14px */ { // .content-sub-heading-12px backward compatibile class
      &:extend(.content-sub-heading);
      font-size: 14px !important;
    }

    &.x-small,
    /* &-12px */ { // .content-sub-heading-12px backward compatibile class
      &:extend(.content-sub-heading);
      font-size: 12px;
    }
  }

  // @TODO: remove commenting if this breaks the title styles
  // &.gravur {
  //   font-size: 30px;
  // }
}

h1, h2, h3, h4, h5, h6,
.heading {
  font-size: 24px;
  font-weight: normal;
  line-height: 36px;
  // .tight-spacing();
  .heading-classes;

  .standard.row & {
    //margin-top: @line-height-computed / 2;
    //margin-bottom: @line-height-computed / 2;
    padding-bottom:  @tcom-col-content-padding;
    margin-bottom: 0;
  }
}

// the following is no longer limited to div/span
// div, span {
  // reset styles
  &.panel-heading,
  &.sub-heading,
  &.content-heading,
  &.content-sub-heading {
    // .tight-spacing();
    display: block;

    // .standard.row & {
    //   margin-top: @line-height-computed / 2;
    //   margin-bottom: @line-height-computed / 2;
    // }

    div &,
    span & {
      padding-bottom: @tcom-col-content-padding;
    }
  }

  .heading-classes;
// }

div, span {

}

