.disclaimer {
  .collapse {
    display:block;
    height: auto !important;
    @media @small-screen {
      display: none;
      height: 0 !important;
    }
  }

  a.collapse-bar,
  .collapse-content {
    margin:0 0;
  }

  .collapse-content {
    background: transparent;
  }

  .collapse-bar {
    display:none;
  }

  &.standard.row.content {
    .col.collapse-content {
        padding:0 @tcom-global-content-padding;
    }
  }
  @media @small-screen {
    a.collapse-bar {
        display:block;
    }

    h2 {
      display:none;
    }

    .collapse-content {
      background: @tcom-white;
    }

    .collapse-content p:nth-child(2) {
      margin-top: @tcom-global-content-spacing*2;
    }

    .collapse.in {
      display: block;
      height: auto !important;
    }
  }
}

.fine-print,
.disclaimer {
  h2, h3, h4, h5, h6,
  h2&, h3&, h4&, h5&, h6& {
    &:extend(p.small);
    line-height: 18px !important;
    font-weight: bold !important;
    //margin-bottom: 18px !important;
  }

  &,
  p&,
  span& {
    &:extend(p.small);
    line-height: 18px;
    color: @tcom-grey-light;

    a:not(.collapse-bar) {
      color: @tcom-grey-light !important;
      text-decoration: underline;

      &:hover,
      &:focus {
            color: @tcom-grey-dark !important;
      }
    }
  }

  ul,ol {
    padding: 0 20px;

    li {
      margin-bottom: 12px;
    }
  }
}

// list
.col-wrapper ul,
.col-wrapper ol {
  padding-left: 25px;

  li {
    margin-bottom: 6px;
  }

  &.no-space li {
    margin-bottom: 0px;
  }
}



// list of links
.links-list {
  list-style: none;
  padding: 0;

  li {
    margin-bottom: 11px;
    line-height: 14px;
  }

  a {
    color: @text-color;
    font-size: 12px;
    vertical-align: top;
  }
}

.links-list.light a {
  color: @tcom-text-light;
}

.align-bottom {
  position: absolute;
  bottom: 0px;
}

.clear-both {
  float: none;
  clear: both;
}

//Links for PDF docs
.content-link-list,
//Override .col-wrapper ul { padding-left: 25px; }
.col-wrapper .content-link-list {
  list-style: none;
  margin:0;
  padding:0;

  a:before{
    .td-icon-family(icon-ui-right-arrow);
    margin-left: -4px;
  }
}


// breadcrumbs
.breadcrumb {
  font-size: @tcom-footer-font-size;
  line-height: @tcom-footer-line-height;

  > li.pull-right:before {
    content: '';
  }
}
.breadcrumb > li + li:before {
  content: '>';
  font-weight: normal;
}


// utility functions
.scale-height { height: 100%; }
.scale-width { width: 100%; }
.position-top { top: 0; }
.position-left { left: 0; }
.position-bottom { bottom: 0; }
.position-right { right: 0; }
.position-vertical-center {}
.position-horizontal-center {}

// jquery placeholder
.placeholder {
  color: #aaa !important;
}

label {
  cursor: pointer;
}

.highlight-grey {
  color: lighten(@gray-dark, 25.7%);
  background-color: @gray-lighter;
}

.no-margin {
  &,
  & * {
    margin-top: 0 !important;
    margin-right: 0 !important;
    margin-bottom: 0 !important;
    margin-left: 0 !important;
  }
}

.no-upper {
  text-transform : none !important;
}

.img-responsive {
  display: inline-block;
  height: auto !important;
}