.alert-primary {
 .generate-theme(s; background-color, light);
}

.alert {
  position: relative;
  padding-right: @tcom-global-content-padding*2 + 26px;
}

.close {
  .alert & {
    position: absolute;
    top: @tcom-global-content-padding;
    right: @tcom-global-content-padding;
  }

  .alert-success & {
    background-color: @brand-success;
    &:hover, &:focus {
      background-color: darken(@brand-success, 20%);
    }
  }

  .alert-info & {
    background-color: @brand-info;
    &:hover, &:focus {
      background-color: darken(@brand-info, 20%);
    }
  }

  .alert-warning & {
    background-color: @brand-warning;
    &:hover, &:focus {
      background-color: darken(@brand-warning, 20%);
    }
  }

  .alert-danger & {
    background-color: @brand-danger;
    &:hover, &:focus {
      background-color: darken(@brand-danger, 20%);
    }
  }

  .alert-custom &,
  .alert-custom-dark &,
  .alert-custom-preview & {
    background-color: rgba(0, 0, 0, .20);
    &:hover, &:focus {
      background-color: rgba(0, 0, 0, .40);
    }
  }
}

.alert-custom-dark {
  color: @tcom-white;
}

.alert-custom-preview {
  background-color: @tcom-green-dark;
  color: @tcom-white;
  position: fixed;
  z-index: 99999;
  top: 70px;
  right: 30px;

  a {
    color: @tcom-white;
    line-height: 2;
  }
}
