@collapsed-breadcrumb-width: 25px;
@delimiter-padding: 8px;
@delimiter-overlap: 20px;

#footer,
.footer {
  background-color:@tcom-footer-bg;
}


/* Signature
----------------------------------*/
.footer-signature {
  background: @tcom-light-grey;
}
body.white-bkg .footer-signature {
  background: #fff;
}



/* Breadcrumbs
----------------------------------*/
.footer-crumb {
  position: relative;
  background: @tcom-white;

  .standard.row.content .col .col-wrapper {
    padding: @tcom-col-content-padding;
  }

  .crumb-wrapper {
    width: 100%;
    overflow: hidden;

    &.with-survey {
      width: 70%;
    }
  }

  .row.content {
    margin-bottom: 0;
  }

  .breadcrumb {
    background: @tcom-white;
    margin-bottom: 0;
    padding: 0; 
    width: 5000px;
    
    .itemscope {
      float: left;
      a {
        white-space: nowrap;
        float: left;
      }
      &.current-page {
        font-weight: bold;
      }
      .delimiter { 
        padding: 0 @tcom-global-content-spacing 0 @delimiter-padding; 
      }

      &.collapsed {
        a {
          max-width: @collapsed-breadcrumb-width;
          overflow: hidden;
          transition: max-width 1s ease-in-out;

          &:hover,
          &:focus {
            max-width: 1000px;

            & + .delimiter {
              background: transparent;
              padding-left: @delimiter-padding;
              margin-left: 0;
            }
          }
        }

        .delimiter {
          background-color: transparent;
          background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 50%);
          padding-left: @delimiter-overlap + @delimiter-padding;
          margin-left: -1 * @delimiter-overlap;
        }

        &:hover,
        &:focus {
          a {
            max-width: 1000px;
          }
          .delimiter {
            background: transparent;
            padding-left: @delimiter-padding;
            margin-left: 0;
          }
        }
      }
    }
  }

  a {
    color: @text-color;
  }

  .active {
    color: @text-color;
    font-weight: bold;
  }
}
/* Fix for IE8 bug with max-width and overflow (see http://snook.ca/archives/html_and_css/ie8-bug-max-width-overflow) */
.lt-ie9 .footer-crumb .breadcrumb .itemscope.collapsed {
  max-width: 2 * @collapsed-breadcrumb-width;
  overflow: hidden;

  &:hover {
    max-width: 1000px;
  }
}

// Mobile
@media (max-width: @screen-sm-max) {
  .footer-crumb {
    .crumb-wrapper.with-survey {
      width: 100%;
    }
    .breadcrumb {
      width: auto;

      .itemscope a {
        white-space: normal;
      }
    }
  }
}

/* Footer links
----------------------------------*/

// Mobile
@media (max-width: @screen-sm-max) {
  .footer-links {

    &.container {
      padding-bottom: 0 !important;
    }

    ul{
      float: left; 
      margin-bottom: 0;

      li {
        float: left;
        height: initial;
        margin-bottom: @tcom-col-content-margin;
        width: 100%;
        line-height:@tcom-col-content-margin !important;
      }
    }

    ul.social-icons {
      padding-top:@tcom-col-content-margin + @tcom-col-gutter * 2;
      float: left;
      width: @tcom-col-content-margin * 8 !important;

      li {
        width: 25px;
        height: 25px;
        margin-right: @tcom-col-content-margin * 2 !important;
        margin-bottom: @tcom-col-content-margin * 2 !important;
      }
    }

    .standard.row.content {
      .col {
        width: 50%;
        float: right;

        .col-wrapper {
          margin-left: 0;
          margin-right: 0;
          padding-left: 0;
          padding-right: 0;
          float: left;
        }

        &.first{
          position: absolute;
          top: @tcom-col-content-margin * 10;
        }
      }
    }
  }

  .responsive-switch {
    margin: 0 -30px;
    text-align: center;
    line-height: 50px;
    border-top: 1px solid @tcom-content-border;
    font-size: 14px;
    margin-top: 0;
  }
}

// Desktop
.footer-links {
  
  padding: 0 30px;

  &.container{
    padding-top: 10px;
    padding-bottom: 100px;
    background-color:@tcom-footer-bg;

    .content{
        background-color:@tcom-footer-bg;
    }

    .col.first .col-wrapper {
      padding-left: 0;
    }
  }

  ul {
    &:extend(.links-list all);

    a {
      color: @tcom-text-light;

      &.header {
        font-weight: bold;
      }
    }

    &.social-icons {
      .clearfix();

      li {
        float: left;
        margin-right: 20px;
      }
    }
    
  }

  .social-icon {
    background-size: 25px 25px;
    display: inline-block;
    width: 25px;
    height: 25px;
  }

  .backgroundsize & .social-icon {
    background-size: 25px 25px !important;
  }

  .social-icons-mix(facebook);
  .social-icons-mix(twitter);
  .social-icons-mix(youtube);
  .social-icons-mix(google);
  .social-icons-mix(linkedin);

  .responsive-switch {
    color: @tcom-white;
    font-size: 12px;
    cursor: pointer;
    margin-top: 40px;

    .td-icon {
      font-size: 22px !important;
    }
  }
}