.standard.content.row.videos {
  margin-bottom: 0;

  .row .col-wrapper & {
    .col-wrapper {
      padding-top: 0;
      padding-bottom: 0;
    }
  }

  .row.content.transparent & {
    background-color: transparent ;
    border: 0 none;
    .box-shadow(0 0 0 rgba(0, 0, 0, 0));
  }
}

.video-wrap {
  margin: 0 auto;

  @media @small-screen {
    width: auto !important;
  }
}

.video-player {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px; 
  height: 0; 
  overflow: hidden;

  @media @small-screen {
    height: 0 !important;
  }
}
 
.video-player iframe,
.video-player object,
.video-player embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}