/* ==========================================================================
 * FORM LAYOUT - UPDATED STYLES FOR NBN REFRESH
 * ========================================================================== */

@form-label-color: @tcom-text-dark;

@form-field-color: @tcom-text-default;
@form-field-bg-color: #f5f5f5;
@form-field-border-color: #ddd;

@form-field-padding-top: 10px;
@form-field-padding-bottom: 10px;
@form-field-padding-h: 25px;
@form-field-min-width: 230px;

@form-ctrl-holder-border-color: #ddd;
@form-ctrl-holder-padding-v: 25px;

/* ==========================================================================
 * - FORM WRAPPER
 * ========================================================================== */

.new-form {
  fieldset {

  }

  legend {
    .font-size(24, 30);
    .font-gravur();
    border: none;
    margin: 0;
  }

  .ctrl-holder,
  .ctrl-group {
    border-bottom: 1px solid @form-ctrl-holder-border-color;
  }

    .ctrl-group {
      padding-top: @form-ctrl-holder-padding-v;
    }

    .ctrl-group .ctrl-holder {
      border-bottom: none;
      display: inline-block;
      margin-right: 70px;
      padding-top: 0;
    }
}

/* ==========================================================================
 * - CTRL HOLDER
 * ========================================================================== */

.ctrl-holder {
  margin: 0;
  padding: @form-ctrl-holder-padding-v 0;
  .clearfix();

  &:first-child {
    margin-top: 0;
  }
}

/* ==========================================================================
 * - FORM LABEL
 * ========================================================================== */

.ctrl-holder {
  label,
  .label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    .font-size(16, 24);
    .font-gravur();

    &.is-empty {
      display: none;
    }

    .with-tcom-theme({
      color: @secondary;
    });
  }
}

/* ==========================================================================
 * - TEXT/SELECT/TEXTAREA
 * ========================================================================== */

.ctrl-holder {
  .text,
  select,
  textarea,
  .options,
  .option {
    display: block;
    text-align: left;
    clear: both;
    color: @form-label-color;
    font-weight: bold;
    .font-size(13, 24);
  }

  .text,
  select,
  textarea {
    color: @form-field-color;
    background-color: @form-field-bg-color;
    border: 1px solid @form-field-border-color;
    -webkit-appearance: none;
    -moz-appearance: none;
    outline: none;
    padding: @form-field-padding-top @form-field-padding-h @form-field-padding-bottom @form-field-padding-h;
    border-radius: 0;
    transition-property: color, background-color, border;
    transition-duration: 250ms;
    min-width: @form-field-min-width;

    &:focus {
      .with-tcom-theme({
        border-color: @secondary;
        background-color: mix(#fff, @primary, 90%);
      });
    }

    &:disabled {
      opacity: 0.5;
    }

    .lt-ie9 &.is-disabled {
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
    }
  }

  .text {
    overflow: visible;
    float: left;
    padding-top: 0;
    padding-bottom: 0;
    height: 42px;
    line-height: 42px;
  }

  .lt-ie10 & .text {
    height: 40px;
    line-height: 40px;
  }

  select {
    padding-right: @form-field-padding-h + 15px;
    background: @form-field-bg-color url(../img/telstra/forms/select-arrow-2x.png) no-repeat 100% 50%;
    background-size: 30px 15px;

    .no-backgroundsize & {
      background-image: url(../img/telstra/forms/select-arrow.png);
    }

    &::-ms-expand {
      visibility: hidden;
    }

    .lt-ie10 & {
      padding-left: 15px;
      padding-right: 10px;
      background: none;
      background-color: @form-field-bg-color;
      background-size: auto auto;
    }
  }

  textarea {
    height: auto;
    min-height: 120px;
    resize: none;

    .lt-ie10 & {
      min-height: 0;
      height: 200px;
    }
  }
}

/* ==========================================================================
 * - RADIO/CHECKBOX
 * ========================================================================== */

.ctrl-holder {
  .options {
    margin: 0;
    padding: 0;

    &.is-horizontal {
      .clearfix();

      .option {
        float: left;
        margin: 0;
        padding-right: 10px;
        padding-bottom: 10px;
        clear: none;
      }

      label,
      .label {
        width: auto;
        max-width: none;
        padding-right: 30px;
      }
    }
  }

  .option {
    position: relative;
    margin-top: 10px;

    &:first-child {
      margin-top: 0;
    }

    input {
      position: absolute;
      top: 5px;
      left: 0;

      .opacity & {
        opacity: 0;
      }
    }

    label {
      width: auto;
      padding: 0 0 0 30px;
      margin: 0;
      color: @form-field-color;
      font-family: @font-family-helvetica;
      text-transform: none;
      display: inline-block;
      .font-size(13, 24);

      .no-opacity & {
        padding-left: 20px;
      }

      &:before {
        position: absolute;
        top: 2px;
        left: 0;
        width: 18px;
        height: 18px;
        border: 1px solid @form-field-border-color;
        text-align: center;
        content: '';
        background-color: #fff;
        border-radius: 2px;
        transition-property: background-color, border-color;
        transition-duration: 250ms;
        .font-size(16, 17);

        .with-tcom-theme({
          color: @secondary;
        });

        .no-opacity & {
          display: none;
        }
      }
    }

    input:checked,
    input.is-checked {
      & + label {
        &:before {
          .td-icon-family(icon-thick-tick, block);

          .no-opacity & {
            display: none;
          }
        }
      }
    }

    input[type="radio"] + label {
      &:before,
      &:after {
        border-radius: 50%;
      }
      &:after {
        content: '';
        transition-property: background-color, border-color;
        transition-duration: 250ms;
      }
    }

      input[type="radio"]:checked,
      input[type="radio"].is-checked {
        & + label {
          &:before {
            content: ''; // To override the default 'tick'
            .with-tcom-theme({
              border-color: @secondary;
            });
          }
          &:after {
            .with-tcom-theme({
              background-color: @secondary;
            });
            width: 10px;
            height: 10px;
            position: absolute;
            left: 4px;
            top: 6px;
          }
        }
      }

    input:focus {
      & + label {
        &:before {
          .with-tcom-theme({
            border-color: @secondary;
            background-color: mix(#fff, @primary, 90%);
          });
        }
      }
    }
  }
}

/* ==========================================================================
 * - FANCY RADIO/CHECKBOX
 * ========================================================================== */

.ctrl-holder {
  .options.is-fancy .option,
  .option.is-fancy {
    label {
      border: 1px solid @form-field-border-color;
      background-color: @form-field-bg-color;
      padding: 20px 60px 20px 30px;
      color: @tcom-text-default;
      cursor: pointer;
      border-radius: 0;
      transition-property: color, background-color, border;
      transition-duration: 250ms;
      .font-size(16, 24);
      .font-gravur();

      .td-icon {
        margin-right: 10px;
        color: @tcom-text-default !important; //to override the important base color style
        top: -2px;
        transition: color 250ms;
        .font-size(24);
      }
    }

    input {
      top: 26px;
      right: 30px;
      left: auto;
    }

    input + label:before {
      top: 22px;
      right: 30px;
      left: auto;
    }

    .with-tcom-theme({
      input:focus,
      input:checked {
        & + label {
          border-color: @secondary;

          &:before {
            //border-color: @form-field-border-color;
            background-color: @form-field-bg-color;
          }
        }
      }

      input:checked {
        & + label {
          color: @secondary;

          .td-icon {
            color: @secondary !important; //to override the important base color style
          }

          &:hover {
            color: #fff;

            .td-icon {
              color: #fff !important; //to override the important base color style
            }
          }
        }
      }

      label:hover {
        color: #fff;
        border-color: @secondary;
        background-color: @secondary;

        .td-icon {
          color: #fff !important; //to override the important base color style
        }
      }
    });
  }
}
