.carousel {
  &.carousel-border {
    border: 1px solid @tcom-grey-lighter;
  }
  &.carousel-border-theme {
    border: 1px solid transparent;
    .generate-theme(s; border-color, light);
  }
  img{
    display: none;
  }
  div:first-child{
    img{
      display: block;
      width: 100%;
      margin: auto;
    }
  }
}

// Default Variables

@slick-font-path: "./fonts/";
@slick-font-family: "td-icons";
@slick-loader-path: "./";
@slick-arrow-color: @tcom-grey-dark;
@slick-dot-color: black;
@slick-dot-color-active: @slick-dot-color;
// @slick-prev-character: '\2190';
// @slick-next-character: '\2192';
@slick-dot-character: '\2022';
@slick-dot-size: 6px;
@opacity-default: .75;
@opacity-on-hover: 1;
@opacity-not-active: 0;


/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;

  img{
    display: block;
  }
  

  &:focus {
    outline: none;
  }

  .slick-loading & {
    background: #fff url("../img/telstra/ajax-loader-big.gif") center center no-repeat;
  }

  &.dragging {
    cursor: pointer;
    cursor: hand;
  }
}
.slick-slider .slick-list,
.slick-track,
.slick-slide,
.slick-slide img {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  zoom: 1;

  &:before,
  &:after {
    content: "";
    display: table;
  }

  &:after {
    clear: both;
  }

  .slick-loading & {
    visibility: hidden;
  }
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  [dir="rtl"] & {
    float: right;
  }
  img {
    display: block;
    width: 100%;
    margin: auto;
  }


  &.slick-loading img {
    display: none;
  }

  display: none;

  &.dragging img {
    pointer-events: none;
  }

  .slick-initialized & {
    display: block;
  }

  .slick-loading & {
    visibility: hidden;
  }

  .slick-vertical & {
    display: block;
    height: auto;
    border: 1px solid transparent;
  }

  // custom backgrounds
  &.bg-colour-light {
    .generate-theme(s; background-color, light);
  };
  &.bg-colour-dark {
    .generate-theme(s; background-color, dark);
  };
  &.bg-colour-gradient {
    .generate-theme(s; background, linear-gradient, dark, light);
  };
  &.bg-colour {
    background-color: @tcom-grey-lighter;
  };
  &.bg-grey-gradient {
    background: -webkit-linear-gradient(@tcom-grey, @tcom-grey-lighter);
    background: linear-gradient(@tcom-grey, @tcom-grey-lighter);
  };

  // custom gallery class
  .carousel-gallery-list & {
    button {
      border: 0;
      background: none;
      padding: 0;
      outline: none;
      margin: 0 3px;
      border: 3px solid transparent;

      + button {
        margin-left: -3px;
      }

      &.active {
        border: 3px solid @tcom-grey-lighter;
      }
    }
  }
}


/* Arrows */

.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 25px;
  width: 25px;
  margin-top: -12px;
  line-height: 0;
  font-size: 0;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  padding: 0;
  border: none;
  outline: none;
  z-index: @zindex-carousel;

  @media @small-screen {
    height: 40px;
    width: 40px;
    margin-top: -20px;
  }

  &:hover, &:focus {
    outline: none;
    background: transparent;
    color: transparent;
    &:before {
      opacity: @opacity-on-hover;
    }
  }
  &.slick-disabled:before {
    opacity: 0;

    .no-opacity & {
      display: none;
    }
  }
}
.slick-prev:before, .slick-next:before {
  font-family: @slick-font-family;
  font-size: 25px;
  line-height: 1;
  color: @slick-arrow-color;
  opacity: @opacity-default;
  .transition(opacity .3s ease);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  @media @small-screen {
    font-size: 40px;
  }
}
.slick-prev {
  left: 10px;
  [dir="rtl"] & {
    left:  auto;
    right: 10px;
  }
  &:before {
    // content: @slick-prev-character;
    .td-icon-char(icon-ui-left-arrow-thick);
    @media @small-screen {
      .td-icon-char(icon-ui-left-arrow-round);
    }
    [dir="rtl"] & {
      // content: @slick-next-character;
      .td-icon-char(icon-ui-right-arrow-thick);
    }
  }
}
.slick-next {
  right: 10px;
  [dir="rtl"] & {
    left:  10px;
    right: auto;
  }
  &:before {
    // content: @slick-next-character;
    .td-icon-char(icon-ui-right-arrow-thick);
    @media @small-screen {
      .td-icon-char(icon-ui-right-arrow-round);
    }
    [dir="rtl"] & {
      // content: @slick-prev-character;
      .td-icon-char(icon-ui-left-arrow-thick);
    }
  }
}

/* Dots */

// .slick-slider {
//   margin-bottom: 30px;
// }
.slick-dots {
  position: absolute;
  bottom: 10px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  right: 20px;
  z-index: @zindex-carousel;
  line-height: 0;

  @media @small-screen {
    position: static;
    margin: @tcom-global-content-spacing 0;
  }

  li {
    position: relative;
    display: inline-block;
    // height: 10px;
    // width: 10px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;

    button {
      border: 0;
      background: transparent;
      display: block;
      height: 10px;
      width: 10px;
      outline: none;
      line-height: 0;
      font-size: 0;
      color: transparent;
      padding: 5px;
      cursor: pointer;

      background: @tcom-grey-lighter;
      border: 1px solid @tcom-grey-lighter;
      border-radius: 10px;

      .lt-ie9 & {
        background: @tcom-grey-lighter;
      }

      &:hover, &:focus {
        outline: none;
        &:before {
          opacity: @opacity-on-hover;
        }
      }

