.lh-nav,
.lh-nav-submenu,
.flyout-menu {
  list-style: none;
  padding: 0 15px;
  margin: 0 0 0 -15px;
  background: #fff;

  padding: 20px 0 0 0;
  font-size: 12px;

  li {
    margin-bottom: 0 !important;
  }

  > li {
    position: relative;
    border-top: 1px solid #eee;

    > a {
      display: block;
      color: @tcom-text-dark;
      line-height: 18px;
      padding: 4px 15px 4px 20px;
      

      &:hover,
      &:focus,
      &:active {
        background-color: #f4f4f4;
        text-decoration: none;
      }
    }
  }
  
  > .context > a {
    font-weight: bold;
    border-left: 10px solid #909090;
    padding-left: 10px;
  }

  > .context.active > a{
    background: url(../img/telstra/lh-nav-active.png) no-repeat -10px 0px;
  }
}

.lh-nav { 
  > li:last-of-type,
  > li.last {
    border-bottom: 1px solid #eee;
  }
}

.lh-nav-submenu {
  margin: 0;
  padding: 0;
  border-left: 10px solid #dadad4;

  > li:last-of-type > a,
  > li.last > a {
    border-bottom: 0;
  }

  > .context > a {
    border-left: 0;
    padding-left: 20px;
  }
  > .context.active > a {
    background: url(../img/telstra/lh-nav-active-child.png) no-repeat -10px 0px;
  }
}

.lh-nav-submenu .lh-nav-submenu { // 3rd level menu
  border-left: 0;

  > li > a:before {
    content: '- ';
  }
}

.flyout-menu {
  display: none;
  margin: 0;
  padding: 0;
  background: @tcom-grey-dark;
  width: 175px;
  position: absolute;
  left: 100%;
  top: 0;
  z-index: 1010;

  > li {
    border-top: 1px solid @tcom-grey-light;
  }

  > li > a {
    color: @tcom-text-light;
    padding: 4px 10px;

    &:hover,
    &:focus,
    &:active {
      background-color: @tcom-grey;
    }
  }

  > li:first-of-type,
  > li.first {
    border-top: 0;
  }

  .hover > & {
    display: block;
  }

  &.flyout-bottom {
    top: auto;
    bottom: 0;
  }
}

.flyout-toggle {
  padding-right: 20px !important; // provides padding for arrow

  &:after {
    .td-icon-family(icon-ui-play);
    position: absolute;
    display: block;
    top: 0;
    right: 0;
    line-height: 18px;
    padding: 4px 4px;

    font-style: normal;
    font-weight: normal;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}