/* ==========================================================================
 * FEATURE POPOVER
 * ========================================================================== */

.feature-popover {
  display: none;
  position: absolute;
  z-index: @zindex-modal;
  outline: none;

  &.is-active {
    display: block;
  }

  &.is-fixed {
    position: fixed;
  }

  .popover-content {
    padding: 15px 20px;
    background: #f8f5f8;
    max-width: 300px;
    min-width: 180px;
    z-index: 0;
    position: relative;
    bottom: -4px;
  }

  .popover-arrow {
    .triangle(up, 24px, 9px, #f8f5f8);
    margin-left: -12px;
    position: absolute;
    top: -17px; // 2 * 9px - 1px
    left: 50%;
    z-index: 1;
    pointer-events: none;
  }
}
