// --------------------------------------------------
// Video Module Styles
// --------------------------------------------------

.video-panel-desc,
.video-panel {

  width: 100%;
  margin-left: auto;
  margin-right: auto;

  .col-100 & {
    max-width: 400px;

    @media @small-screen {
      max-width: 100%;
    }  
  }

  .lt-ie9 & {
    max-width: 400px;
  }

}

.video-panel {

  height: 220px;
  position: relative;

  background-color: @tcom-white;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;

  button {

    position: absolute;
    top: 0%;
    left: 0%;
    height: 100%;
    width: 100%;

    background-color: transparent;
    border: none;
    outline: none;
    font-size: 48px; 

    i { 
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);

      .lt-ie9 & { 
        margin-left: -24px;
        margin-top: -24px;
      }

      color: @tcom-white !important; 
    }

    &:hover,
    &:focus {
      i { 
        color: @tcom-grey-lighter !important; 
      }
    }

  }

}

.video-panel-desc {
  overflow: hidden;
  padding-top: @tcom-global-content-padding;

  .video-run-time {
    float: right;
    padding-left: @padding-base-horizontal;
    color:  @tcom-grey-light;
  }

  .video-panel-play-btn {
    display: block;
    margin-top: @padding-large-vertical;

    .with-tcom-theme({
      color: @primary;
    });

    &:before {
      .td-icon-family(icon-ui-right-arrow);
    }
  }
}

.video-panel-grad {
  
  &:before {
    display: block;
    content: "";
    height: 220px;
    width: 100%;
    opacity: .5;
    position: absolute;
    top: 0;
    left: 0;
    background-size: 100% 100%;
  }

  // Need to generate a new svg ?
  // http://ie.microsoft.com/TEStdrive/Graphics/SVGGradientBackgroundMaker/Default.html
  .base-purple &,
  .base-default &,
  .base-blue & {
    &:before {
      background-image: url(../img/telstra/svg-gradient/blue-lefttoright.svg);
    }
    filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#80644bc8', endColorstr='#800099D2',GradientType=1 );
  }

  .base-orange & {
    &:before {
      background-image: url(../img/telstra/svg-gradient/orange-lefttoright.svg);
    }
    filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#80e20070', endColorstr='#80ff8700',GradientType=1 );
  }

  .base-green &,
  .base-turquoise & {
    &:before {
      background-image: url(../img/telstra/svg-gradient/green-lefttoright.svg);
    }
    filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#80009182', endColorstr='#80bcfb00',GradientType=1 );
  }
  
  .base-magenta & {
    &:before {
      background-image: url(../img/telstra/svg-gradient/magenta-lefttoright.svg);
    }
    filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#80E20070', endColorstr='#80640064',GradientType=1 );
  }
}


.video-module {
  position: relative;
  padding-bottom: 56.25%;
  //padding-top: 30px; why ?
  height: 0; 
  overflow: hidden;

  @media @small-screen {
    height: 0 !important;
  }
}
 
.video-module iframe,
.video-module object,
.video-module embed,
.video-module .innerWrapper {
  position: absolute !important;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}