//
// Telstra theme based on brand 2.1
// --------------------------------------------------
//
// Q&A:
//  Why do you use both colour and color in the mix?
//  Well... colour is used in class names and color is used elsewhere. Get it?
//

// T.com colour themes base colours
// --------------------------------------------------
// @tcom-grey
// @tcom-orange
// @tcom-green
// @tcom-turquoise
// @tcom-blue
// @tcom-purple
// @tcom-magenta
// --------------------------------------------------

// panel background colours
// --------------------------------------------------
.standard.row.content {
  .grey-gradient {
    h1, h2, h3, h4, h5, h6 {
      .generate-theme(s; color, default);
    }
  }

  h2, h3, h4, h5, h6 {
    &.light-colour {
      .generate-theme(s; color, light);
    }

    &.dark-colour {
      .generate-theme(s; color, dark);
    }
  }

  .col {
    &.colour-gradient {
      .col-wrapper {
        .generate-theme(m; background, linear-gradient, light, dark; border-color, default);
      }
    }

    &.light-colour {
      .col-wrapper {
        .generate-theme(m; background-color, light; border-color, light);
      }
    }

    &.dark-colour {
      .col-wrapper {
        .generate-theme(m; background-color, dark; border-color, dark);
      }
    }
  }
}

// Tranaparency is common @>--
.transparent {
  &.col {
    .col-wrapper {
      background-color: transparent !important;
    }
  }
}

// Grey gradient colour remains the same regardless of the theme colour
.col {
  &.white {
    .col-wrapper {
      background-color: @tcom-white !important;
      border-color: @tcom-white !important;
    }
  }

  &.grey-gradient {
    .col-wrapper {
      .gradient(@tcom-white, @tcom-grey-lighter);
      // background-color: darken(@tcom-light-grey, 5%) !important;
    }
  }
}

// Common foreground colours
.light-colour,
.dark-colour,
.colour-gradient {
  &.col {
    h1, h2, h3, h4, h5, h6,
    p,
    a,
    b, i, strong, em,
    span {
      color: @tcom-light-on-dark-color;
    }
  }
}

