.price-base()   {font-size: 1em;}
.price-xsmall() {font-size: 1.5em;}
.price-small()  {font-size: 1.71428571em;}
.price-medium() {font-size: 3.42857143em;}
.price-large()  {font-size: 5.14285714em;}
.price-units()  {font-size: 0.5em; line-height: 1em;}

.price,
.bytes-hertz {
  &-gravur {font-family: @font-family-gravur;}

  & {.price-base(); /*font-size: 1em;*/} //14px

  &-xsmall {.price-xsmall();} //14px to 21px
  &-small  {.price-small();}  //14px to 24px
  &-medium {.price-medium();} //14px to 48px
  &-large  {.price-large();}  //14px to 72px

  &-xsmall,
  &-small,
  &-medium,
  &-large {
    .unit,
    .cents {.price-units();}
  }

  &-promo {
    display: inline-block;
    color: @tcom-grey-light;
    position: relative;

    &:after {
      position: absolute;
      content: "";
      display: block;
      width: 100%;
      top: 50%;
      left: 0;
      border-bottom: 1px solid @tcom-grey-light;
    }
  }
}

.price {
  &-xsmall,
  &-small,
  &-medium,
  &-large {
    .unit,
    .cents {
      display: inline-block;
      margin-top: .61em;
      vertical-align: top;
    }
  }
}


