// styles for popup template

.popup {
  background-color: @tcom-white;
  height: 100%;

  .container.main-content {
    width: 100% !important;
  }
}

.popup-header {
  width: 100%;
  position: fixed;
  margin: 0 -30px;
  z-index: 1100;
  background: @tcom-white;

  @media (max-width: @screen-sm-min) {
    width: 100%;
  }
}

.popup-header > div{
  height: 70px;
  background: url(../img/telstra/logo-blue.png) no-repeat 20px 15px;
  border-bottom: 4px solid #757575;
  margin-bottom: 10px;
}

.popup-content {
  margin-top: 80px;
}