/* Mobile
------------------------------------ */

@media (max-width: @screen-sm-max) {
  .tab-content {

    margin-top: @tcom-col-content-margin;
    margin-bottom: @tcom-col-content-margin;

    a.collapse-bar {
      margin-top: 0;
      margin-bottom: 1px;
      height: auto;

      .subtitle {
          display: block;
          font-size: 14px;
          line-height: 21px;
          //padding-bottom: @tcom-global-content-spacing;
          //margin-top: -@tcom-global-content-spacing;
      }

      &:after {
        top: 50%;
        margin-top: -25px;
      }

      // & + .content.row.tab-pane.collapse-content {
      & + .tab-pane.collapse-content {
        &.collapsing, &.collapse.in{
          display: block;
          height: auto;
        }
      }

      // &.collapsed + .content.row.tab-pane.collapse-content {
      &.collapsed + .tab-pane.collapse-content {
        height: 0;

        .v11 & {
          border: none !important;
        }
      }

    }

    .tab-pane.collapse-content {
      margin-top: -1px !important;
      margin-bottom: 1px !important;
      //padding-left: 0;
      //padding-right: 0;
    }
  }

  .tabs-vertical .tabs-content-wrapper {
    background-color: transparent !important;

    .tab-content.vertical {
      margin-top: 0;
      margin-bottom: 0;

      .collapse-bar{
        .generate-theme(s; background-color, dark);
        color: @tcom-white !important;

        &.collapsed {
          .generate-theme(s; background-color, dark);
        }
      }
    }
  }

}

/* Desktop
------------------------------------ */

.tab-content {
  margin-bottom: @tcom-row-margin !important;

  .collapse-content {
    overflow: hidden;
    margin-top: 0;
  }

}


.nav-tabs-columns(@columns: 2, @margin: 1%) {
  @total-margins: (@columns - 1) * @margin;
  @col-width: (100% - @total-margins)/@columns;

  > li {
    min-width: @col-width;
    max-width: @col-width;
    margin-right: @margin;

    &.nav-tabs-last {
      min-width: @col-width;
      max-width: @col-width;
      margin-right: 0;
      float: right;
    }
  }
}

/* Vertical Tabs Row
---------------------------*/
.tabs-vertical {
  margin-bottom:20px;

  & .tabs-content-wrapper{
    background-color:@tcom-white;

    .content.row {
      margin-bottom: 0;
    }
  }

  .standard.row.content & {
    .nav {
      padding-left: 0;
    }

    .col {
      padding: 0 @tcom-global-content-padding;
    }
  }
}

/* Tabs Navigation
---------------------------*/

.nav-tabs {
  margin: 0 -15px;
  border: 0;
  clear:both;
  > li {

    margin-right: 0.8%;

    > a {
      background: #dadada;
      margin-right: 0;
      border: 0;
      padding: 15px;

      font-size: 24px;
      line-height: 25px;
      color: #575757;
      //height: 65px;

      .subtitle {
        display: block;
        font-size: 14px;
        margin-top: 2px;
        margin-bottom: -5px;
        line-height:21px;
      }

      &:hover,
      &:focus {
        background: #757575;
        color: #fff;

        /* TEL-2745: adding a white color for the icons on hover */
        > i {
          color: @tcom-white !important;
        }
      }

      &:active {

      }
    }

    &.active > a,
    &.active > a:hover,
    &.active > a:focus {
      color: #575757;
      background: #fff;
      border: 0;

      > i {
        color: #575757 !important;
      }
    }
  }
}

.v11 {
  .nav-tabs-switch {
    margin-bottom: 0px;
  }
  .nav-tabs {
    //margin: 0;
    margin-left: -@tcom-col-content-margin;
    margin-right: -@tcom-col-content-margin;
    border-bottom: 1px solid #bababa;
    > li.active {
      border: 1px solid #bababa;
      border-bottom: 0;
    }

     li {
            margin-bottom: -1px;
            a {
                font-size:14px;
            }
     }

  }

  .tab-content {
    //margin: 0 15px;

  }

  .tab-pane {
    border: 1px solid #bababa !important;
    border-top: 0 !important;
    margin-left: -@tcom-col-content-margin;
    margin-right: -@tcom-col-content-margin;
  }
}


  /* Vertical Tabs Nav
  -------------------------*/
.nav-tabs.vertical {
  overflow:hidden;

  > li {
    width:100%;
    margin-right:0;
    border-bottom:1px solid @tcom-white;
    background: none;

    &.nav-tabs-last, &.nav-tabs-last.active {
      border-bottom:0;

      a{
        border-bottom:0px solid @tcom-white;
      }
    }

    & a{
      font-size: 18px;
      line-height: 24px;
      padding: 20px;
    }

    &.active a{
        background: @tcom-white !important;
        .generate-theme(s; color, dark);
        border-bottom:1px solid @tcom-white;

        &:hover{
          background: @tcom-white;
          .generate-theme(s; color, dark);
        }
    }

    & a{
        .generate-theme(s; background-color, dark);
        color: @tcom-white;
        border-bottom:1px solid @tcom-white;

        &:hover{
          .generate-theme(s; background-color, light);
          color: @tcom-white;
        }
     }
  }

}

// Switch tabs
// -----------------------------------------
.nav-tabs-switch {
  margin-bottom: 20px;

  .subtitled .subtitle {
    margin-top:10px;
  }

  .nav-tabs-columns(2, 1.5%);

  &.nav-tabs-3 {
    .nav-tabs-columns(3, 1.5%);
  }

  &.nav-tabs-4 {
    .nav-tabs-columns(4, 1.5%);
  }
}

// Switch tabs
// without accordion on mobile
// -----------------------------------------
@switch-withoutAccordion-tableSpacing : 5px;

@media @small-screen {
  .nav-tabs-switch-withoutAccordion {
    margin: 0 -(@tcom-global-content-padding + @switch-withoutAccordion-tableSpacing );

    .nav-tabs-switch {
      table-layout: fixed;
      display: table;
      border-spacing: @switch-withoutAccordion-tableSpacing;
      width: 100%;
      margin: 0 0 @tcom-row-group-margin 0;

      &:before,
      &:after {
        content: none;
      }
    }

    li {
      float: none !important;
      display: table-cell;
      background: @tcom-grey-lighter;

      a {
        background: transparent;
      }

      &:hover,
      &:focus {
        background: @tcom-grey-light;

        a,
        a i {
          color: @tcom-white !important;
        }
      }

      &.active {
        background: @tcom-white;

        a,
        a i {
          color: @tcom-grey !important;
        }
      }
    }
  }
}


.switchmodule .tab-content > .tab-pane.standard.row.content {
  background-color: transparent;
}

.collapse-content,
.tab-pane {
    height: 0;
    overflow: hidden;
    display: block !important;
    margin-bottom: 0 !important;
    &.in,
    &.active {
      height: auto;
  }
}

.support .tab-pane {
  height: auto;

  @media @small-screen {
    display: none !important;

    &.active {
      display: block !important;
    }
  }
}
