//== Media queries breakpoints
//
//## Define the breakpoints at which your layout will change, adapting to different screen sizes.
@screen-xs: 768px;
@screen-sm: 768px;
@screen-md: 768px;
@screen-lg: 768px;

// prevent scroll on mobile breakpoint
@media @small-screen {
  body {
    overflow-x: hidden;
  }

  xmp.prettyprint {
    display: none;
  }
}