
@lh-mobile-grid-spacing : 4px;
@countdown-size: 70px;
@countdown-size-inner: @countdown-size - 10;

.page-takeover {
  position: relative;
  margin-top: 20px;
  margin-bottom: 20px;
  // #spectrum background image's height   :438px
  // - header.no-header's margin-bottom    : 20px
  // - .page-takeover's margin-top         : 20px
  // -------------------------------------------
  // = .page-takeover's min-height         : 398px
  min-height: 398px;
  .clearfix();

  @media @small-screen {
    min-height: 1px;
    margin-top: 0;
  }

  &.without-margin {
    margin-bottom: 0;
  }

}

#clock {
  margin: 15px 0;

  .clock-face {
    color: @tcom-grey-light;
    position: relative;
    width: @countdown-size;
    height: @countdown-size;
    display: inline-block;
    margin-right: 10px;
    border-radius: @countdown-size;
    .generate-theme(s, background-color, light);
  }

  .clock-time {
    height: @countdown-size-inner;
    width: @countdown-size-inner;
    border-radius: @countdown-size-inner;
    background-color: @tcom-white;
    margin: 5px;
    text-align: center;

    font-size: 22px;
    font-weight: bold;
    padding-top: 18px;

    position: absolute;
    top: 0;
  }

  .clock-text {
    position: absolute;
    width: @countdown-size;
    top: 18px;
    text-align: center;
    font-size: 8px;
    text-transform: uppercase;
  }
}

.page-takeover-content {

  > h1 {
    font-size: 36px;
  }

  #takeover-title {
    // spacing between the top of the takeover and the top of the title has to be 70px
    // requirement                           : 70px
    // - header.no-header's margin-bottom    : 20px
    // - .page-takeover's margin-top         : 20px
    // -------------------------------------------
    // = #takeover-title margin-top           : 30px
    //   rounded to 25px due to the line-height
    margin-top: 25px;
  }

  > .btn.primary {
    margin-top: 20px;
  }

  &.hidden {
    visibility: hidden;
  }
}

.page-takeover-links {
  width: 230px;
  position: absolute;
  right: 0;
  top: 0;
  .clearfix();

  @media @small-screen {
    position: static;
    width: 100%;
    padding-top: @lh-mobile-grid-spacing;
    padding-left: @lh-mobile-grid-spacing;
    .generate-theme(s; background-color, default);

    .flyout-menu {
      display: none;
    }
  }

  .td-icon {
    font-size: 26px;
    margin: 0 10px 0 -2px;
    line-height: 18px; // same as text
    vertical-align: top;

    @media @small-screen {
      display: block;
      font-size: @tcom-icon-sm;
      margin: 0;
      line-height: 1;
      margin-bottom: 5px;
    }

  }

  .fb-like {
    position: absolute;
    right: 10px;
    top: 10px;
  }

  .lh-nav {
    margin: 0;
    padding: 0;
    background: transparent;

    .td-icon {
      color: inherit !important;
    }
    
    > li {
      background: @tcom-white;
      border: 0;
      margin-bottom: 1px !important;

      @media @small-screen {
        width: 25%;
        float: left;
        margin-bottom: 0 !important;
        border-right: @lh-mobile-grid-spacing solid transparent;
        border-bottom: @lh-mobile-grid-spacing solid transparent;
        background: transparent;
      }

      &.hover > a:after {
        .td-icon-family(icon-ui-down-arrow);
        @media @small-screen {
          display: none;
        }

      }
      
      &.page-takeover-head > a {
        &:hover {
          background-color: @tcom-white !important;
          .generate-theme(s; color, dark);
        }
        &:after {
          content: '';
        }
      }

      > a {
        padding: 12px 20px;
        font-size: 18px;
        .generate-theme(s; color, dark);
        height: 44px;
        line-height: 22px;
        cursor: pointer;

        @media @small-screen {
          padding: 6px 10px !important;
          height: 90px;
          text-align: center;
          font-size: 12px;
          line-height: 14px;
          background: @tcom-white;
        }

        &:hover {
          .generate-theme(s; background-color, default);
          color: @tcom-white;
        }

        &:after {
          .td-icon-family(icon-ui-right-arrow);
          position: absolute;
          right: 10px;
          top: 14px;
          padding: 0;
          font-size: 26px;

          @media @small-screen {
            display: none;
          }
        }
      }
    }
  }

  .flyout-menu {
    left: 0;
    top: 100%;
    width: 100%;
    background: transparent;

    > li {
      border: 0;
      margin-top: 1px !important;
    }

    > li > a {
      padding: 12px 20px;
      font-size: 14px;
      height: 44px;
      .generate-theme(s; background-color, dark);
      color: @tcom-white;

      &:hover {
        .generate-theme(s; background-color, default);
      }

      &:after {
        .td-icon-family(icon-ui-right-arrow);
        position: absolute;
        right: 10px;
        top: 14px;
        padding: 0;
        font-size: 26px;
      }
    }
  }

  .flyout-toggle:after {
    content: '';
  }
}

#flyout-mobile-menu {
  button.close {
    background: #fff;
  }
  button.close:after {
    .generate-theme(s; color, default) !important;
  }
  .modal-title {
    visibility: hidden;
  }
  .modal-body {
    padding-bottom: 75px;
  }
  .modal-content {
    .generate-theme(s; background-color, default);
  }
  .mobile-menu-wrapper {
    float: right;
    position: relative;
    left: -50%;
  }
  ul.mobile-menu {
    margin: 0;
    padding: 0;
    text-align: center;
    position: relative;
    left: 50%;

    li {
      float:left;
      margin:0;
      padding:0;
      list-style:none;
      border-right:solid 1px #fff;
      width: 89px;
      height: 100px;

      &:last-child {
        border-right:none;
      }

      a {
        color:#fff;
        display: block;
        padding: 10px;
        &:hover {
          text-decoration: none;
        }

        i {
          color:#fff !important;
          display: block;
          font-size: 42px;
        }
      }
    }
  }
}



