/*
 * Style tweaks
 * --------------------------------------------------
 */
@support-icon-font: 26px;
@support-icon-padding: 5px;
@support-icon-border: 2px;
@support-icon-size: @support-icon-font + ((@support-icon-padding + @support-icon-border) * 2);
@support-triangle: 8px;



#page-header {
  min-height: 65px;
  margin-top: 30px;
  margin-bottom: 10px;

  @media @small-screen {
    height: auto;
    margin: 0;
    position: relative;
  }

  .page-title {
    > a,
    > h1 > a,
    .site-title {
      white-space: nowrap;
      color: @tcom-text-light;
      .font-gravur(36);
      display: inline-block;

      &:hover {
        text-decoration: none;
      }

      @media @small-screen {
        white-space: normal;
      }
    }

    > h1,
    > a,
    .site-title {
      margin: 6px 0 10px 0;
      @media @small-screen {
        margin: 20px 0;
      }
    }
  
    > a {
      line-height: 1.1;
    }

    // @media @small-screen {
    //   padding-right: @support-icon-size + (@tcom-global-content-padding * 2);
    // }
  }

  .page-toolbar {
    text-align: right;

    @media @small-screen {
      position: absolute;
      top: 20px;
      right: 0;
      margin-top: -5px;
    }

  }

  .contact-share {
    width: 49%;

    .dropdown-menu,
    .dropdown-toggle {
      width: 100%;
      text-align: left;
    }

    .dropdown-toggle:before {
      display: none; // no icon on desktop
    }

    &.open .dropdown-toggle,
    .dropdown-toggle:hover {
      background: #fff;
      color: @tcom-default-dark;
      .generate-theme(s; color, dark);
    }

    + .contact-share {
      margin-left: -3px;
    }


    @media @small-screen {
      width: auto;

      &.page-toolbar-share {
        display: none;
      }

      &.page-toolbar-contact {

        .dropdown-toggle {
          background: transparent;
          overflow: hidden;
          width: @support-icon-size;
          height: @support-icon-size;
          border-radius: 5px;
          border: @support-icon-border solid @tcom-white;
          padding: @support-icon-padding;
          float: right;
          color: transparent;
          
          &:active,
          &:focus {
            color: transparent;
            background: rgba(255, 255, 255, .3);
          }
      

          &:before {
            .td-icon;
            display: inline-block;
            font-size: @support-icon-font;
            color: @tcom-white !important;
          }
          &:after {
            display: none;
          }
        }

        .dropdown-menu {
          margin-top: @tcom-global-content-padding;
          right: -@tcom-global-content-padding;

          a {
            background-color: rgba(0, 0, 0, .8);
            margin: 0;

            &:after {
              display: none;
            }
          }
          &:before {
            display: block;
            content: "";
            position: absolute;
            right: (@support-icon-size / 2) + @tcom-global-content-padding - (@support-triangle / 2);
            top:-8px;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 0 @support-triangle @support-triangle @support-triangle;
            border-color: transparent transparent rgba(0, 0, 0, .8) transparent;
            -webkit-transform:rotate(360deg);
          }
        }
      }
    }
  }
}
