#browser-warning {
  display: none;
}

.lt-ie8 #browser-warning {
  display: block;
  background: @tcom-globalnav-bg;
  padding-bottom: @tcom-global-content-padding;
  border-bottom: 1px solid @tcom-content-border;

  .icon-information {
    display: block;
    padding: 30px 0;
    float: left;
    width: 50px;
    zoom: ~"expression(this.runtimeStyle['zoom']='1',this.innerHTML='\e64f')";
  }

  // .pull-left {
  //   width: 70%;
  // }

  #dismiss-warning {
    float: right;
    margin-top: 50px;
  }

  &.dismissed {
    display: none;
  }

  a {
    text-decoration: underline;
    color: @tcom-text-light;
  }
}