/* ==========================================================================
 * SHADE BACKGROUND
 * ========================================================================== */

.shade-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: @zindex-shade;
  background-color: #000;
  opacity: 0;

  &.is-active {
    display: block;
  }
}
