/* Desktop
-------------------------- */
#scrollup {
	width: 100%;
	position: fixed;
	bottom: 100px;
	z-index: @zindex-scrollup;
	background: @tcom-white;
	transition: opacity 0.3s ease;

	&.affix-top {
		opacity: 0;
	}

	&.affix {
		opacity: 1;
	}
}

.scrollup-outer {
	position: relative;
}

.scrollup {
	width: 100px;
	height: 60px;
	text-align: center;
	font-size: 14px;
	text-decoration:none;
	color: @tcom-grey-dark;
	background: @tcom-white;
	right: -130px;
	position: absolute;
	cursor: pointer;
	font-weight: bold;

  .white-bkg & {
    background: @tcom-light-grey;
  }

	&:before{
		.td-icon-family(icon-ui-up-arrow);
		font-size: 24px;
		text-align: center;
		width: 100%;
		height: 22px;
		display: block;
		margin-bottom:10px;
		font-weight: normal;
	}
}

@media @small-screen {
	#scrollup {
		position: static;

		&.affix-top {
			opacity: 1 !important;
		}
	}

	.scrollup {
		position: static;
		margin: auto;
		width: 120px;
		padding-top: 20px;

	  .white-bkg & {
	    background: @tcom-white;
	  }
  
		&:before {
			width: 24px;
			margin-top: -5px;
			float: right;
			font-weight: bold;
		}
	}
}
