/* ==========================================================================
 * - SITE SEARCH TYPEAHEAD
 * ========================================================================== */

// Search menu results container and popover toggle
@media @small-screen {
  .site-search {
    width: auto;
    background-color: transparent;

    &.feature-popover .popover-content {
      top: 0;
      padding: 0;
      height: 52px;
      max-width: none;
      background-color: transparent;
      width: 237px;

      input {
        .border-radius(0); // Override iOS rounded corners
        .font-gravur();
        .font-size(16);
        -webkit-appearance: none;
        float: left;
        height: 52px;
        padding: 0 15px;
        border: none;
        outline: none;
        background-color: white;
        color: inherit;
        width: 185px;


        .lt-ie9 & {
          padding-top: 13px;
        }
      }

      button.btn-search {
        position: absolute;
        border: none;
        right: 0;
        top: 0;
        .generate-theme(s; background-color, light);

        &:hover, &:focus {
          .generate-theme(s; background-color, dark);
        }

        &:active {
          .generate-theme(s; background-color, darken, dark, 10%);
        }
      }

      .popover-close {
        position: absolute;
        right: -52px;
        top: 0;
        background-color: black;

        &:hover, &:focus {
          .generate-theme(s; background-color, dark);
        }
        &:active {
          .generate-theme(s; background-color, darken, dark, 10%);
        }

        .td-icon {
          .font-size(20);
        }
      }
    }
  }

  // Typeahead dropdown and contained elements
  .twitter-typeahead {
    width: 100%;

    .tt-dropdown-menu {
      position: relative;
      clear: left;
      width: 289px;
   

      // To be added to search bars with fixed position parent (enables scrollable search results)
      &.overflow-scroll {
        overflow: scroll;
        overflow-x: hidden;
      }

      // Title separators for search sections
      .search-results-title {
        .font-gravur();
        .font-size(12);
        width: 100%;
        padding: 12px 20px;
        background-color: @tcom-grey-medium;

        &:hover {
          color: @tcom-text-default;
        }
      }

      // No results found for empty search result arrays
      .no-results-found {
        width: 100%;
        background-color: #fff;
        padding: 10px 20px;

        p {
          margin: 0;
        }
      }

      .tt-suggestion {
        padding: 0 !important;
        width: 100%;
        background-color: #fff;

        &.tt-cursor,
        a:hover,
        a:focus {
          background-color: lighten(@tcom-grey-lighter, 10%);
          cursor: pointer;

          a {
            .generate-theme(s; color, dark);
          }
        }

        &:active {
          .generate-theme(s; background-color, darken, dark, 10%);
        }

        a {
          text-decoration: none;
          transition: none;
          color: @tcom-text-default;

          &.search-list-result {
            margin: 0;
            padding: 10px 20px;
            text-overflow: ellipsis;
            white-space: nowrap !important;
            width: 100%;
            display: block;
            overflow: hidden;

            strong {
              color: @tcom-text-dark;
            }
          }
        }

        // Icon, title, search text block for 'suggestions' search partition
        .suggested-item {
          position: relative;
          width: 100%;

          .icon-container {
            height: 100%;
            width: 90px;
            position: absolute;
            left: 0;

            img {
              display: block;
              position: relative;
              height: 50px;
              margin: 0 auto;
              top: 50%;
              margin-top: -25px;
            }
          }

          .content-container {
            width: 100%;
            padding: 15px 15px 15px 90px;
            border-top: 1px solid @tcom-light-grey;

            &:first-of-type {
              border-top: none;
            }

            p {
              margin: 0;
              line-height: 18px;
              text-decoration: none;

              // // Confine to two lines of text only using ellipsis
              // text-overflow: ellipsis;
              // display: block;
              // display: -webkit-box;
              // -webkit-line-clamp: 2;
              // -webkit-box-orient: vertical;
              // overflow: hidden;
            }

            // // Confine title text to one line of text only using ellipsis
            // strong {
            //   white-space: nowrap;
            //   overflow: hidden;
            //   text-overflow: ellipsis;
            //   display: inline-block;
            //   width: 100%;
            // }
          }
        }
      }
    }
  }
}