/* ==========================================================================
 * - PRICE AND DATA
 * ========================================================================== */

.price-and-data {
  position: relative;
  padding-top: 12px;
  padding-bottom: 12px;

  .font-size(40, 48);
  .clearfix();

  .smlxl-v2-item & {
    margin: 25px 0;
  }

  .pd-price,
  .pd-data {
    font-weight: 700;
    float: left;
    width: 50%;
    letter-spacing: -2px;
  }

  .pd-info {
    .font-size(14, 14);
    letter-spacing: 0;
    position: absolute;

    &.is-pre {
      top: 0;
    }

    &.is-post {
      .font-size(12, 12);
      bottom: 0;
    }
  }

  .pd-price {
    padding-right: 5px;
  }
  

  .pd-price-value {
    .with-tcom-theme({
      color: @secondary;
    });

    sup {
      top: -0.7em;
      padding-right: 1px;
      .font-size(20, 20);
    }

    sub {
      width: 20px;
      display: inline-block;
      margin-left: 4px;
      font-weight: 400;
      bottom: 0;
      top: auto;
      letter-spacing: 0;
      .font-size(10, 10);
    }
  }

  .pd-data {
    padding-left: 10px;
    border-left: 1px solid @tcom-grey-medium;
    color: @tcom-text-dark;
  }

  .pd-data-value {
    sup {
      top: -1.15em;
      margin-left: 2px;
      letter-spacing: 0;
      .font-size(15, 15);
    }
  }

}
