@media @big-screen {
  //
  // Primary navigation
  // --------------------------------------------------
  #primary-nav {
    z-index: @zindex-megamenu;

    .container {

      .navbar-collapse {
        padding-left: 0;
        padding-right: 0;
      }

      .navbar-form {
        padding-right: 0;
        padding-left: 0;
      }

      .navbar-right {
        margin-right: 0 !important;
      }

    }

    &.navbar {
      background-color: #fff;
      border: 0;
    }

    .search-telstra {
      padding-left: 5px;
      margin-left: 3px;
      background-color: lighten(@tcom-grey, 51.5%); // #dadada :-)
      display: block;

      .search-telstra-input {
        border: 0;
        background-color: transparent;
        box-shadow: none;
        -webkit-box-shadow: none;
        font-size: 16px;
        color: @tcom-grey;
        padding-left: 5px;
        padding-right: 35px;
      }

      .search-telstra-btn {
        margin-right: 5px;
        font-size: 20px;
        background: transparent;
        border: 0;
        color: #aaa;
      }
    }
  }

  // site title and logo
  .site-title {

    .font-gravur(17; bold);

    a & {
      color: @tcom-grey-light;
    }
    
    span.site-logo {
      margin-left:10px;
    }

    .navbar-nav > li > a& {
      padding:5px 1px;
      display:inline-block;
      line-height: 30px;
      float: left;

      &:hover,
      &:focus {
        background: none;
      }
    }
  }

  .site-logo {
    display: inline-block;
    //height: 28px;
    width: 28px;
    background: url(../img/telstra/telstra-logo.png) no-repeat;
    background-position: -90px -3px;

    .base-orange & {
        background-position: 0 -3px
    }

    .base-green & {
        background-position: -30px -3px
    }

    .base-turquoise & {
        background-position: -60px -3px
    }

    .base-blue & {
        background-position: -90px -3px
    }

    .base-purple & {
        background-position: -120px -3px
    }

    .base-magenta & {
        background-position: -150px -3px
    }
  }

  //
  // Mega Menu
  // --------------------------------------------------
  #mega-menu {
    color: @tcom-text-light;

    > li {
      position: static; // so we can break out width with submenus
      
      > a {
        border-left: 1px solid transparent;
        .generate-theme(s; border-color, default);
        padding-left: 10px;
        padding-right: 10px;
        color: @tcom-text-dark;

        &:after {
          .td-icon-family(icon-ui-down-arrow);
          margin-left: 6px;

          .generate-theme(s; color, dark);

          .navbar-linear& {
            content: none;
          }
        }

        &:focus {
          //background: @tcom-white;
          //outline: none;
        }
      }

      &:last-of-type > a {
        border-right: 1px solid transparent;
        .generate-theme(s; border-color, default);
      }

      &.active > a,
      &.hover > a,
      > a:hover,
      > a:focus {
        .generate-theme(s; background-color, dark);
        color: @tcom-text-light;

        &:after {
          color: @tcom-white;
        }
      }

      &.hover {
        .mega-menu-view {
          display: block;
        }
      }
    }
    
    .mega-menu-view-bottom {
      .generate-theme(s; background-color, default);
      .clearfix();
    }

    .mega-menu-view-top {
      .generate-theme(s; background-color, dark);
    }

    // mega menu icons
    .td-icon-sm {
      color: @tcom-white !important;
      font-size: 32px;
      margin-left: -4px;
    }

  }

  // mega menu view area
  .mega-menu-view {
    position: absolute;
    left: 0px; // counters padding
    right: 0;
    margin: 0 auto;
    z-index: @zindex-megamenu;
    display: none;

    padding: 0 @tcom-global-content-padding;
    width: @container-lg;

    a,p {
      color: @tcom-text-light;
      font-size: 12px;
    }

    .mega-menu-heading,
    .mega-menu-group-heading {
      font-size: 12px;
      font-weight: bold;
      display: block;
      margin: 10px 0;
    }

    .mega-menu-group-heading {
      margin-top: @tcom-col-gutter * 2;
      padding-left: @tcom-col-gutter * 2;
      margin-bottom: 0;

      & ~ .mega-menu-list {
        padding-top: @tcom-col-gutter;
      }
    }
  }

  // mega menu list of links
  .mega-menu-list {
    padding: 20px 20px;
    width: 20%;
    display: inline-block;
    vertical-align: top;
    text-align: left;

    ul {
      list-style: none;
      padding: 0;

      li {
        margin-bottom: 11px;
        line-height: 14px;
      }

      a {
        color: @tcom-text-light;
        font-size: 12px;
        vertical-align: top;
      }
    }
  }



  // mega menu list of inline links
  .mega-menu-list-inline {
    width: 20%;
    float: left;
    padding: @tcom-global-content-spacing @tcom-global-content-spacing*2;

    li {
      line-height: 32px;
      height: 32px;
      margin: @tcom-global-content-spacing 0;
    }

    i {
      margin-right: 5px;
    }
  }

  .mega-menu-icon-link {
    display: inline-block;
    text-align: center;
    vertical-align: top;

    & {
      width: 163px; //< 7 per line
    }
    .seven-icons & {
      width: 140px; //7 per line
    }
    .nine-icons & {
      width: 109px; //9 per line

      .td-icon-md {
        font-size: 55px;
        height: 55px;
      }
    }

    .td-icon-md {
      display: block;
      height: 70px;
      width: 65px;
      margin: 20px auto 15px auto;
      color: @tcom-white !important;
    }

    p {
      margin-top: 10px;
    }

    &:hover {
      text-decoration: none;

      p {
        text-decoration: underline;
      }
    }
  }

  // mega menu footer items
  // 3 items by default
  .mega-menu-footer {
    padding: 15px 5px 15px 20px;
    .clearfix();
    float: left;
    width: 33.32%;

    .td-icon-sm {
      margin: 10px 25px 0 15px !important;
      height: 70px;
      float: left;
    }

    .col-xs-5 {
      white-space: nowrap;
      padding-left: 20px;
      padding-right: 0;
    }
  }

  // mega menu footer items
  // override to display 4 items
  .mega-menu-footer-by-4 {
    padding: @tcom-global-content-padding;
    width: 25%;

    .td-icon-sm {
      margin: 10px @tcom-global-content-padding 0 0 !important;
    }  

    .col-xs-7,
    .col-xs-5 {
      width: auto;
    }

    .col-xs-5 {
      float: right;
    }
  }

  #shade {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: black;
    z-index: @zindex-shade;
    opacity: 0.5;
    filter: alpha(opacity=50);
    display: none;

  &.active {
    backface-visibility: hidden;
    display: block;
    }
  }
}