@media @big-screen {
  .site-header {
    display: none !important;
  }
}


@media @small-screen {
  /* ==========================================================================
   * HEADER
   * ========================================================================== */

  /* ==========================================================================
   * - NAV TOGGLE
   * ========================================================================== */

  .nav-toggle {
    display: block;
    height: 60px;
    position: relative;
    outline: none;
    border: none;
    background-color: transparent;
    padding: 0;
    .clearfix();
    width: 42px;


    .menu {
      .font-gravur();
      .font-size(16, 24);
      font-weight: bold;
      color: @tcom-globalnav-menu-text;
      text-decoration: none;
      margin-top: 10px;
      width: 44px;
      text-align: left;
      transition: color 250ms;
      float: left;
      margin-top: 18px;
      .visuallyhidden();
    }

    .icon {
      display: block;
      position: relative;
      width: 22px;
      height: 60px;
      float: right;
    }

    .icon-bar {
      background-color: @tcom-globalnav-menu-text;
      width: 22px;
      height: 3px;
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: -8px;
      border-radius: 3px;
      transition: background-color 250ms;

      & + .icon-bar {
        margin-top: -2px;

        & + .icon-bar {
          margin-top: 4px;
        }
      }
    }

    &:hover,
    &:focus,
    &:active {
      .with-tcom-theme({
        .icon-bar {
          background-color: @primary;
        }

        .menu {
          color: @primary;
        }
      });

      .icon-bar {
        .generate-theme(s; background-color, light);
      }

      .menu {
        .generate-theme(s; color, light);
      }
    }
  }

  /* ==========================================================================
   * - SITE LOGO
   * ========================================================================== */

  .site-logo-position(@index, @size: 40) {
    background-position: unit(-(@index * @size), px) 0;

    &.is-light {
      background-position: unit(-(@index * @size), px) unit(-@size, px);
    }
   }

  .site-logo-positions(@size: 40) {
    background-size: unit(@size * 4, px) unit(@size * 2, px);

    .base-magenta & {
      .site-logo-position(0, @size);
    }

    .base-blue & {
      .site-logo-position(1, @size);
    }

    .base-green & {
      .site-logo-position(2, @size);
    }

    .base-orange & {
      .site-logo-position(3, @size);
    }
  }

  .site-logo {
    display: block;
    width: 40px;
    height: 40px;
    background: transparent url(../img/telstra/logos/telstra-2x.png) no-repeat;
    background-position: 0 0;
    .site-logo-position(1);
    .site-logo-positions();

    .no-backgroundsize & {
      background-image: url(../img/telstra/logos/telstra.png);
    }

    &.is-small {
      width: 26px;
      height: 26px;
      .site-logo-position(1, 26);
      .site-logo-positions(26);

      .no-backgroundsize & {
        background-image: url(../img/telstra/logos/telstra-small.png);
      }
    }
  }

  /* ==========================================================================
   * - HEADER LAYOUT
   * ========================================================================== */

  .site-header {
    padding: 15px 0 10px 0;
    color: @tcom-text-dark;
    background-color: @tcom-white;
    padding: 20px 0 15px 0;
    position: relative;

    .nav-toggle,
    .site-logo {
      float: left;
    }

    .nav-toggle {
      margin: -10px 17px -10px 0;
      padding-right: 20px;

      &:after {
        content: '';
        position: absolute;
        top: 10px;
        right: 0;
        width: 1px;
        height: 40px;
        border-right: 1px solid #cacaca;

        .rgba & {
          border-right-color: rgba(0,0,0,0.1);
        }
      }
    }

    .site-title {
      float: left;
      margin: 0 0 0 10px;
      color: @tcom-text-dark;
      text-decoration: none;
      transition: color 250ms;
      .font-gravur();
      .font-size(19, 40);
    }

    .site-action {
      color: @tcom-text-dark;
      text-decoration: none;
      outline: none;
      display: block;
      transition: color 250ms;

      .td-icon {
        color: @tcom-text-dark !important;
        top: -2px;
        margin-right: 5px;
        .font-size(22);
        transition: color 250ms;
      }

      &:hover,
      &:focus,
      &:active {
        .with-tcom-theme({
          color: @primary;

          .td-icon {
            color: @primary !important;
          }
        });
      }

      span {
        .visuallyhidden();
      }
    }

    .site-actions {
      float: right;
      list-style: none;
      margin: 0;
      padding: 0;
      .font-gravur();
      .font-size(16, 40);

      & > li {
        float: left;
        margin: 0 0 0 20px;
      }

      .has-tel {

        .d-phone & {
          display: block;
        }

        display: block;

        .td-icon {
          display: none;
        }
      }

      .btn-search {
        margin-top: -5px;
      }
    }

    &.has-dark-bg {
      color: @tcom-white;

      .nav-toggle {
        .icon-bar {
          background-color: @tcom-white;
        }
      }

      .site-action {
        color: @tcom-white;

        .td-icon {
          color: @tcom-white !important;
        }
      }
    }
  }

  /* ==========================================================================
   * - FIXED HEADER
   * ========================================================================== */

  @nav-toggle-menutext-offset-fixed: -2px;

  .site-header.is-fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: @tcom-globalnav-fixed-bg none;
    color: @tcom-white;
    padding: 12px 0;
    opacity: 0;
    transition-duration: 250ms;
    transition-property: opacity transform;
    z-index: @zindex-navbar-fixed;
    display: none;

    &.active {
      display: block;
      opacity: 1;

      &.headroom--top {
        display: none !important; // To override velocity display set in scrollheader.js
      }
    }

    .menu {
      .font-size(12, 12);
      letter-spacing: 0;
      margin-top: 7px;
      width: 18px;
      color: @tcom-globalnav-fixed-menu-text;
    }

    .nav-toggle {
      width: 42px;
      height: 26px;
      margin-top: 0;
      margin-bottom: 0;

      &:after {
        border-right-color: @tcom-globalnav-fixed-border;
        top: 0;
        height: 30px;

        .rgba & {
          border-right-color: rgba(255, 255, 255, 0.1);
        }
      }

      .icon {
        height: 26px;
      }

      .icon-bar {
        background-color: @tcom-globalnav-fixed-menu-text;
        width: 18px;
        height: 2px;
        margin-top: -6px;

        & + .icon-bar {
          margin-top: -1px;

          & + .icon-bar {
            margin-top: 4px;
          }
        }
      }

      &:hover,
      &:focus,
      &:active {
        .icon-bar {
          background-color: @tcom-white;
        }

        .menu {
          color: @tcom-white;
        }
      }
    }

    .site-logo {
      background-color: transparent;
    }

    .site-title {
      .font-size(16, 26);
      color: @tcom-globalnav-fixed-menu-text;
    }

    .site-actions {
      color: @tcom-globalnav-fixed-menu-text;
      .font-size(14, 26);
    }

    .site-action {
      color: @tcom-globalnav-fixed-menu-text;

      .td-icon {
        color: @tcom-globalnav-fixed-menu-text !important;
        .font-size(22);
      }

      &:hover,
      &:focus,
      &:active {
        color: @tcom-white;

        .td-icon {
          color: @tcom-white !important;
        }
      }
    }

    .btn-search {
      background-color: #1d1d1d;
      margin: -12px 0;

      .td-icon {
        .font-size(20, 50);
        color: @tcom-globalnav-fixed-menu-text !important; // This has to be important because default colour is !important in td-icons.css
      }

      &:hover,
      &:focus,
      &:active {
        background-color: black;

        .td-icon {
          color: @tcom-white !important; // This has to be important because default colour is !important in td-icons.css
        }
      }
    }

    &.is-display-ready {
      display: block;
    }

    &.is-displayed {
      opacity: 1;
      .translateHardware(0, 0);
    }
  }
}